.hero-slider__wrapper {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
  perspective: 2000px;
  transform: rotateX(15deg) rotateY(-25deg) rotateZ(5deg);
  transform-style: preserve-3d;
  padding: 0 50px;
  margin: 0 auto;
  position: absolute;
  /* top: -63%; */
  top: -28px;
  left: 0;
  bottom: 0;
  height: 104%;
}

/* .portfolio-wrapper {
  position: relative;
} */

@media (max-width: 392px) {
  .hero-left.blur-vanish-left.visible {
    padding: 50px 0px !important;
  }
}

#home .hero-left {
  width: 50%;
  max-width: 50%;
}

video.hero-video-wrapper {
  top: -10%;
  position: relative;
  height: 120%;
  width: 100%;
  object-fit: cover;
  transform: rotateX(15deg) rotateY(-25deg) rotate(5deg);
}

.hero-right.floating-blur.visible {
  padding-left: 30px !important;
}

.portfolio-wrapper {
  position: absolute !important;
  left: 0;
}

#home .hero-right {
  text-align: center;
  width: 50%;
  height: 100% !important;
  position: absolute;
  right: 0;
}

.portfolio-wrapper {
  height: 100%;
}

.hero-slider__column {
  width: 350px;
  height: 100%;
  position: relative;
  overflow: hidden;
  /* border-radius: 20px; */
  /* box-shadow: 0 25px 80px rgba(0, 0, 0, 0.4); */
  flex-shrink: 0;
}

.hero-slider__column:nth-child(1) {
  z-index: 1;
}

.hero-slider__column:nth-child(2) {
  z-index: 2;
}

.hero-slider__column:nth-child(3) {
  z-index: 1;
}

.hero-slider__track {
  display: flex;
  flex-direction: column;
  animation: heroSliderScroll 25s linear infinite;
  will-change: transform;
}
.hero-slider__track2 {
  display: flex;
  flex-direction: column;
  animation: heroSliderScroll2 30s linear infinite;
  will-change: transform;
}

/* 
        To change delay duration between columns, modify these values:
        - Column 1: animation-delay: 0s (starts immediately)
        - Column 2: animation-delay: -7s (change this for column 2 delay)
        - Column 3: animation-delay: -14s (change this for column 3 delay)
        Increase the negative number for more delay between columns
        */
.hero-slider__column:nth-child(1) .hero-slider__track {
  animation-delay: 0s;
}

.hero-slider__column:nth-child(2) .hero-slider__track {
  animation-delay: -20s;
}

.hero-slider__column:nth-child(3) .hero-slider__track {
  animation-delay: 0s;
}

.hero-slider__track:hover {
  animation-play-state: paused;
}

.hero-slider__slide {
  width: 100%;
  height: fit-content;
  flex-shrink: 0;
  position: relative;
  margin-bottom: 20px;
}

.hero-slider__slide img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
  object-position: top;
  display: block;
}

@keyframes heroSliderScroll {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-2400px);
  }
}

@keyframes heroSliderScroll2 {
  0% {
    transform: translateY(-2400px);
  }
  100% {
    transform: translateY(0);
  }
}

.hero-slider__column::before,
.hero-slider__column::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 120px;
  z-index: 10;
  pointer-events: none;
}

.hero-slider__column::before {
  top: 0;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.8) 0%,
    transparent 100%
  );
}

.hero-slider__column::after {
  bottom: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0%, transparent 100%);
}

.hero .hero-left .subheadline {
  color: white !important;
}

