/* Gallery Loader Styles */
.gallery-loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  z-index: 10;
}

.loader-content {
  text-align: center;
}

.spinner {
  width: 50px;
  height: 50px;
  margin: 0 auto 15px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top-color: #fff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.loader-content p {
  color: #fff;
  font-size: 14px;
  margin: 0;
  font-weight: 500;
}

/* Ensure portfolio wrapper is positioned for absolute loader */
.portfolio-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}
