/* Mobile Slider Styles */

.credibility-container {
  padding: 0 0px;
}

.credibility-slider {
  display: none;
  position: relative;
  /* overflow: hidden; */
  width: 100%;
  margin-top: 20px;
}

.credibility-slider-track {
  display: flex;
  transition: transform 0.3s ease-out;
  width: 100%;
}

.credibility-slide {
  min-width: 100%;
  background: rgba(255, 255, 255, 0.95);
  padding: 30px;
  border-radius: 16px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

/* Navigation Arrows */
.slider-arrow {
  position: absolute;
  top: 115%;
  transform: translateY(-50%);
  background: white;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 24px;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider-arrow:hover {
  background: #f5f5f5;
}

.slider-arrow-left {
  left: 35%;
}

.slider-arrow-right {
  right: 35%;
}

/* Navigation Dots */
.slider-dots {
  display: none;
  justify-content: center;
  gap: 8px;
  margin-top: 24px;
}

.slider-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #d1d5db;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0;
}

.slider-dot-active {
  width: 32px;
  border-radius: 4px;
  background: #4f46e5;
}

/* Mobile Responsive */
@media (max-width: 767px) {
  .credibility-grid {
    display: none;
  }

  .credibility-slider {
    display: block;
  }

  .slider-dots {
    display: flex;
  }
  button.slider-dot {
    display: none;
  }
  
.credibility-container {
  padding: 0 0px;
}
}

/* Tablet and Desktop */
@media (min-width: 768px) {
  .credibility-grid {
    display: grid;
  }

  .credibility-slider {
    display: none;
  }

  .slider-dots {
    display: none;
  }
}
