/* TermsOfService.css */

.termserv-container-wrapper {
  padding: 60px 0;
}

.termserv-content-box {
  max-width: 1000px;
  margin: 0 auto;
}

.termserv-body-content {
  padding: 0;
  color: #333333;
  line-height: 1.6;
}

.termserv-intro-text {
  font-size: 18px;
  color: #333333;
  margin-bottom: 40px;
  line-height: 1.76;
}

.termserv-section-block {
  margin-bottom: 40px;
}

.termserv-section-heading {
  font-size: 24px;
  font-weight: 600;
  color: #1a202c;
  margin: 0 0 16px 0;
  padding-bottom: 10px;
  border-bottom: 2px solid #e2e8f0;
}

.termserv-paragraph-text {
  font-size: 16px;
  color: #333333;
  margin: 0 0 16px 0;
  line-height: 1.7;
}

.termserv-bullet-list {
  margin: 16px 0;
  padding-left: 30px;
}

.termserv-list-item {
  font-size: 16px;
  color: #333333;
  margin-bottom: 10px;
  line-height: 1.7;
}

/* Table Styles */
.termserv-table-container {
  overflow-x: auto;
  margin: 24px 0;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.termserv-pricing-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  font-size: 15px;
}

.termserv-table-head {
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
}

.termserv-table-header {
  padding: 16px 20px;
  text-align: left;
  font-weight: 600;
  color: white;
  font-size: 16px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
}

.termserv-table-body {
  background: white;
}

.termserv-table-row {
  border-bottom: 1px solid #e2e8f0;
  transition: background-color 0.2s ease;
}

.termserv-table-row:last-child {
  border-bottom: none;
}

.termserv-table-body .termserv-table-row:hover {
  background-color: #f7fafc;
}

.termserv-table-cell {
  padding: 16px 20px;
  color: #333333;
  vertical-align: top;
  line-height: 1.6;
}

.termserv-cell-bold {
  font-weight: 600;
  color: #1a202c;
}

/* Contact Box */
.termserv-contact-box {
  background: #ecf2ff;
  border-left: 4px solid #2563eb;
  padding: 24px;
  border-radius: 8px;
  margin-top: 40px;
}

.termserv-contact-title {
  font-size: 20px;
  font-weight: 600;
  color: #1a202c;
  margin: 0 0 12px 0;
}

.termserv-contact-text {
  font-size: 16px;
  color: #333333;
  margin: 0 0 8px 0;
  line-height: 1.7;
}

.termserv-email-link {
  color: #2563eb;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.termserv-email-link:hover {
  color: #764ba2;
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  .termserv-container-wrapper {
    padding: 30px 15px;
  }

  .termserv-body-content {
    padding: 0;
  }

  .termserv-section-heading {
    font-size: 22px;
  }

  .termserv-table-container {
    margin: 20px -25px;
    border-radius: 0;
  }

  .termserv-pricing-table {
    font-size: 12px;
    width: 50%;
    margin: auto;
  }

  .termserv-table-header {
    padding: 12px 15px;
    font-size: 14px;
  }

  .termserv-table-cell {
    padding: 12px 15px;
    font-size: 14px;
  }

  .termserv-contact-text {
    font-size: 14px;
    line-height: 1.6;
  }
}

@media (max-width: 480px) {
  .termserv-intro-text {
    font-size: 14px;
    line-height: 1.6;
  }

  .termserv-section-heading {
    font-size: 20px;
  }

  .termserv-paragraph-text {
    font-size: 14px;
    line-height: 1.6;
  }

  .termserv-list-item {
    font-size: 14px;
  }

  .termserv-pricing-table {
    font-size: 13px;
  }

  .termserv-table-header,
  .termserv-table-cell {
    padding: 8px;
    font-size: 12px;
  }
}
