.swiper-button-prev,
.swiper-rtl .swiper-button-next {
  left: -5px !important;
  left: auto;
}

.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  right: -5px !important;
  left: auto;
}

.testimonials-grid {
    padding: 0 30px;
}

@media (max-width: 1400px) {
  .swiper-button-prev,
  .swiper-rtl .swiper-button-next {
    left: -5px !important;
    left: auto;
  }
 .swiper-button-next,
.swiper-rtl .swiper-button-prev {
  right: -5px !important;
  left: auto;
}
}

.swiper-button-next:after {
  background: #ffffff;
  color: black !important;
}
.swiper-button-prev:after {
  background: #ffffff;
  color: black !important;
}

.swiper-pagination.swiper-pagination-clickable.swiper-pagination-bullets.swiper-pagination-horizontal {
  display: none !important;
}


.swiper-slide {
    padding: 12px !important;
}


.swiper-button-prev, .swiper-button-next {
    top: 46% !important;
}


@media (max-width: 767px) {
  .swiper-button-prev,
  .swiper-rtl .swiper-button-next {
    left: 38% !important;
    top: 102% !important;
  }
  .swiper-button-next,
.swiper-rtl .swiper-button-prev {
    right: 38% !important;
    top: 102% !important;
  }

  .testimonial-quote {
    font-size: 14px;
  }
  .testimonials-grid {
    padding: 0 0px;
}

}
