.footer-container {
  padding-top: 60px;
}

.ttcl-cta-container {
  width: 100%;
  background-color: white;
  padding: 80px 20px;
}

.ttcl-cta-wrapper {
  max-width: 1400px;
  margin: 0 auto 40px auto;
  position: relative;
}

.ttcl-cta-banner {
  position: relative;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  border-radius: 24px;
  padding: 60px 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: visible;
  box-shadow: 0 20px 60px rgba(251, 191, 36, 0.2);
  min-height: 200px;
}

.ttcl-cta-left-image {
  position: absolute;
  left: 14px;
  top: -60px;
  bottom: 0;
  width: 300px;
  z-index: 2;
  pointer-events: none;
}

.ttcl-cta-arrow img {
  width: 100%;
  height: 100%;
}

.ttcl-cta-arrow {
  width: 60px;
  height: 80px;
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

.ttcl-cta-left-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}

.ttcl-cta-right-bg-image {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 350px;
  z-index: 1;
  pointer-events: none;
  opacity: 0.6;
  border-radius: 0 24px 24px 0 !important;
  overflow: hidden;
}

.dotted-bg {
  background: url("../assets/dotted-bg.png");
  width: 50%;
  height: 90%;
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  top: 8px;
  left: 3px;
  z-index: 1;
  opacity: 0.5;
}

.ttcl-cta-right-bg-image::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(81deg, #2563ebab 0%, #1e3c7200 100%);
  z-index: 1;
}

.ttcl-cta-right-bg-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.ttcl-cta-content {
  flex: 1;
  text-align: center;
  padding: 0 80px 0 0;
  position: relative;
  z-index: 3;
  display: grid;
  grid-template-columns: auto auto;
  align-items: center;
  justify-content: flex-end;
  gap: 35px;
}

.ttcl-cta-heading {
  font-size: 2.6rem;
  font-weight: 700;
  color: #ffffff;
  margin: 0;
  line-height: 1.2;
}

.ttcl-cta-arrow {
  display: inline-block;
  flex-shrink: 0;
}

.ttcl-cta-arrow svg {
  width: 70px;
  height: 70px;
  stroke: #ffffff;
  fill: none;
  stroke-width: 3;
  stroke-linecap: round;
}

.ttcl-cta-button-wrapper {
  position: relative;
  z-index: 3;
  flex-shrink: 0;
  width: 18%;
}

.ttcl-cta-button {
  background: white;
  color: #2563eb;
  border: 2px solid white;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s;
  text-decoration: none;
}

.ttcl-cta-button:hover {
  background: #2563eb;
  color: white;
  border-color: #2563eb;
}

.ttcl-cta-button:active {
  transform: translateY(0);
}

@media (max-width: 1200px) {
  .ttcl-cta-heading {
    font-size: 2.25rem;
  }

  .ttcl-cta-left-image {
    width: 250px;
  }

  .ttcl-cta-right-bg-image {
    width: 300px;
  }

  .ttcl-cta-arrow svg {
    width: 60px;
    height: 60px;
  }

  .ttcl-cta-button-wrapper {
    width: 20%;
  }
}

@media (max-width: 1024px) {
  .ttcl-cta-container {
    padding: 60px 20px;
  }

  .ttcl-cta-heading {
    font-size: 1.6rem;
  }

  .ttcl-cta-arrow {
    width: 50px;
    height: 50px;
  }

  .ttcl-cta-left-image {
    width: 268px;
    left: 0;
  }

  .ttcl-cta-right-bg-image {
    width: 280px;
  }

  .ttcl-cta-content {
    padding: 0 30px;
    gap: 15px;
  }

  .ttcl-cta-button {
    padding: 18px 45px;
    font-size: 0.95rem;
  }

  .ttcl-cta-button-wrapper {
    width: 20%;
  }

  a.ttcl-cta-button {
    padding: 14px 22px;
  }
}

@media (max-width: 900px) {
  .ttcl-cta-wrapper {
    padding: 0 20px;
  }

  .ttcl-cta-banner {
    display: block;
  }

  .ttcl-cta-left-image {
    display: none;
  }

  .ttcl-cta-right-bg-image {
    display: none;
  }

  /* .ttcl-cta-right-bg-image::before {
    background: linear-gradient(81deg, #2563ebab 0%, #1e3c7200 100%);
  } */

  .ttcl-cta-content {
    padding: 0;
    margin-bottom: 20px;
    /* gap: 20px; */
    /* align-items: center; */
    display: block;
  }

  .dotted-bg {
    width: 84%;
    height: 100%;
    top: 8px;
    left: 25px;
    opacity: 0.2;
  }

  .ttcl-cta-heading {
    font-size: 1.65rem;
  }

  .ttcl-cta-arrow svg {
    transform: rotate(90deg);
    width: 50px;
    height: 50px;
  }

  .ttcl-cta-button-wrapper {
    margin: 30px 0 20px 0 !important;
    width: 100%;
    height: auto;
    text-align: center;
  }

  .ttcl-cta-arrow {
    display: none;
  }

  .ttcl-cta-banner {
    padding: 40px 20px;
  }
}

@media (max-width: 768px) {
  .footer-container {
    padding-top: 30px;
  }
  .ttcl-cta-container {
    padding: 50px 15px;
  }

  .ttcl-cta-heading {
    font-size: 1.5rem;
  }

  .ttcl-cta-left-image {
    display: none;
  }
}

@media (max-width: 480px) {
  /* .ttcl-cta-banner {
    padding: 25px 20px;
  } */

  .ttcl-cta-heading {
    font-size: 2rem;
  }

  .ttcl-cta-arrow svg {
    width: 45px;
    height: 45px;
  }

  .ttcl-cta-left-image {
    display: none;
  }
}
