.mian-announcement-bar {
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
}

.announcement-bar {
  max-width: 700px;
  margin: auto;
  padding: 15px 0px;
  color: white;
  font-size: 16px;
  text-align: center;
}
.announcement-bar a {
  color: white;
}


@media (max-width: 600px) {
  .announcement-bar {
    font-size: 14px;
    padding: 10px 0px;
  }
}