.case-story-container {
  line-height: 1.6;
  color: #1e293b;
  background: #ffffff;
}

.case-details-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}

/* Hero Banner */
.case-details-hero-banner {
  position: relative;
  height: 550px;
  /* background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); */
  overflow: hidden;
  display: flex;
  align-items: center;
  margin-bottom: 80px;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
}

.case-details-hero-banner:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="rgba%28255,255,255,0.05%29" stroke-width="0.5"/></pattern></defs><rect width="100" height="100" fill="url%28%23grid%29"/></svg>');
  opacity: 0.8;
}

/* .case-details-hero-pattern {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.1;
  background-image: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 35px,
    rgba(255, 255, 255, 0.1) 35px,
    rgba(255, 255, 255, 0.1) 70px
  );
} */

.case-details-hero-content {
  position: relative;
  z-index: 2;
  max-width: 900px;
  color: white;
}

.case-study-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  padding: 10px 20px;
  border-radius: 50px;
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 25px;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.case-details-hero-banner h1 {
  font-size: 56px;
  font-weight: 800;
  margin-bottom: 25px;
  line-height: 1.2;
}

.case-details-hero-banner p {
  font-size: 20px;
  opacity: 0.95;
  max-width: 700px;
}

.case-details-hero-stats {
  display: flex;
  gap: 40px;
  margin-top: 40px;
}

.case-details-stat-item {
  text-align: center;
}

.case-details-stat-number {
  font-size: 42px;
  font-weight: 800;
  display: block;
  color: #59f975;
}

.case-details-stat-label {
  font-size: 14px;
  /* opacity: 0.9; */
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 5px;
}

/* Client Overview Card */
.case-details-client-card {
  background: white;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
  padding: 50px;
  margin: -100px auto 80px 0;
  max-width: 1400px;
  position: relative;
  z-index: 10;
}

.case-details-client-header {
  display: flex;
  align-items: center;
  gap: 30px;
  margin-bottom: 40px;
  padding-bottom: 30px;
  border-bottom: 2px solid #f1f5f9;
}

.case-details-client-logo {
  width: 120px;
  height: 120px;
  background: white;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 42px;
  color: white;
  font-weight: bold;
  box-shadow: 0 10px 30px rgba(102, 126, 234, 0.3);
}

.case-details-client-info h2 {
  font-size: 32px;
  margin-bottom: 10px;
  color: #1e293b;
}

.case-details-client-meta {
  display: flex;
  gap: 25px;
  flex-wrap: wrap;
}

.case-details-meta-item {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #333333;
  font-size: 15px;
}

.case-details-meta-item i {
  color: #2563eb;
  font-size: 18px;
}

p.results-description-intro {
  max-width: 800px;
  margin: auto;
}

.quick-facts {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 25px;
  margin-top: 30px;
}

.fact-box {
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
  padding: 25px;
  border-radius: 15px;
  border-left: 4px solid #2563eb;
}

.fact-box .label {
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #333333;
  font-weight: 600;
  margin-bottom: 8px;
}

.fact-box .value {
  font-size: 18px;
  color: #1e293b;
  font-weight: 600;
}

.case-details-client-logo img {
  width: 100%;
}

/* Content Section */
.case-details-content-wrapper {
  max-width: 1400px;
  margin: 0 auto;
  /* padding: 0 20px 80px; */
}
.case-details-content-wrapper2 {
  max-width: 1400px;
  margin: 0 auto;
  padding: 80px 0 0;
}

.case-details-section {
  margin-bottom: 80px;
  padding: 0 20px;
}

.case-details-section-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 30px;
}

.section-icon {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 26px;
  box-shadow: 0 10px 25px rgba(102, 126, 234, 0.3);
}

.section h2 {
  font-size: 38px;
  color: #1e293b;
  font-weight: 700;
}

.section-intro {
  font-size: 18px;
  color: #333333;
  line-height: 1.6;
  margin-bottom: 40px;
}

/* Challenge Section */
.challenge-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-top: 40px;
}

.challenge-visual {
  background: linear-gradient(135deg, #fee2e2 0%, #fecaca 100%);
  border-radius: 20px;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.challenge-visual::before {
  content: "";
  position: absolute;
  width: 200px;
  height: 200px;
  background: rgba(239, 68, 68, 0.1);
  border-radius: 50%;
  top: -50px;
  right: -50px;
}

.speed-indicator {
  position: relative;
  width: 200px;
  height: 200px;
  margin-bottom: 20px;
}

.speed-circle {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: conic-gradient(#dc2626 0% 19%, #f1f5f9 19% 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 10px 40px rgba(220, 38, 38, 0.2);
}

.speed-inner {
  width: 140px;
  height: 140px;
  background: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.speed-score {
  font-size: 48px;
  font-weight: 800;
  color: #dc2626;
}

.speed-label {
  font-size: 14px;
  color: #333333;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.problem-tag {
  background: white;
  padding: 12px 20px;
  border-radius: 50px;
  font-weight: 600;
  color: #dc2626;
  font-size: 16px;
  box-shadow: 0 5px 15px rgba(220, 38, 38, 0.2);
}

.challenge-list {
  background: white;
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08);
}

.challenge-item {
  display: flex;
  align-items: flex-start;
  gap: 15px;
  padding: 20px;
  margin-bottom: 15px;
  background: #fef2f2;
  border-radius: 12px;
  border-left: 4px solid #dc2626;
  transition: transform 0.3s;
}

.challenge-item:hover {
  transform: translateX(5px);
}

.challenge-item i {
  color: #dc2626;
  font-size: 20px;
  margin-top: 3px;
}

.challenge-item .text {
  flex: 1;
}

.challenge-item strong {
  display: block;
  color: #991b1b;
  margin-bottom: 5px;
  font-size: 16px;
}

.challenge-item p {
  color: #333333;
  font-size: 14px;
  margin: 0;
}

/* Strategy Section */
.strategy-intro {
  text-align: left;
  /* max-width: 800px; */
  /* margin: 0 auto 50px; */
}

.strategy-cards {
  display: grid;
  /* grid-template-columns: repeat(auto-fit, minmax(320px, 1fr)); */
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 50px;
}

.strategy-card {
  background: white;
  border-radius: 20px;
  padding: 35px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 2px solid transparent;
  position: relative;
  overflow: hidden;
}

.strategy-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  transform: scaleX(0);
  transition: transform 0.4s;
}

.strategy-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 60px rgba(102, 126, 234, 0.2);
  border-color: #2563eb;
}

.strategy-card:hover::before {
  transform: scaleX(1);
}

.card-number {
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 22px;
  font-weight: 800;
  margin-bottom: 20px;
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.3);
}

.strategy-card h3 {
  font-size: 22px;
  color: #1e293b;
  margin-bottom: 15px;
  font-weight: 700;
}

.strategy-card ul {
  list-style: none;
  padding: 0;
}

.strategy-card li {
  padding: 12px 0;
  padding-left: 30px;
  position: relative;
  color: #333333;
  font-size: 15px;
  line-height: 1.6;
}

.strategy-card li::before {
  content: "";
  position: absolute;
  left: 0;
  color: #10b981;
  font-size: 14px;
  font-weight: 900;
}

/* Results Showcase */
.results-showcase {
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  /* border-radius: 30px; */
  padding: 80px 0;
  margin: 80px 0;
  position: relative;
  overflow: hidden;
}

.results-showcase:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="rgba%28255,255,255,0.05%29" stroke-width="0.5"/></pattern></defs><rect width="100" height="100" fill="url%28%23grid%29"/></svg>');
  opacity: 0.8;
}

.case-details-result-content-wrapper {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
}

.results-header {
  text-align: center;
  margin-bottom: 60px;
  position: relative;
  z-index: 2;
}

.results-header h2 {
  font-size: 42px;
  color: white;
  margin-bottom: 15px;
}

.results-header p {
  font-size: 18px;
  color: #cbd5e1;
}

.metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 30px;
  /* margin-bottom: 50px; */
  position: relative;
  z-index: 2;
}

.metric-card {
  background: rgb(255 255 255);
  backdrop-filter: blur(10px);
  /* border: 1px dashed white; */
  border-radius: 20px;
  padding: 30px;
  text-align: center;
  transition: all 0.3s;
}

.metric-card:hover {
  /* background: rgba(255, 255, 255, 0.08); */
  transform: scale(1.05);
}

.case-details-metric-label {
  font-size: 14px;
  color: #333;
  text-transform: uppercase;
  /* letter-spacing: 1px; */
  margin-bottom: 15px;
  font-weight: 600;
}

.metric-comparison {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  margin-bottom: 10px;
}

.metric-before {
  font-size: 24px;
  color: #ef4444;
  font-weight: 700;
  text-decoration: line-through;
  /* opacity: 0.6; */
}

.metric-arrow {
  color: #079e6f;
  font-size: 24px;
}

.metric-after {
  font-size: 36px;
  color: #079e6f;
  font-weight: 800;
}

.improvement-badge {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  padding: 6px 15px;
  border-radius: 50px;
  font-size: 13px;
  color: white;
  font-weight: 600;
  display: inline-block;
}

/* Comparison Chart */
.comparison-visual {
  background: white;
  border-radius: 20px;
  padding: 40px;
  position: relative;
  z-index: 2;
}

.chart-row {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 25px;
}

.chart-label {
  min-width: 140px;
  font-weight: 600;
  color: #1e293b;
  font-size: 15px;
}

.chart-bars {
  flex: 1;
  display: flex;
  gap: 15px;
  align-items: center;
}

.bar-before {
  height: 30px;
  background: linear-gradient(90deg, #ef4444 0%, #dc2626 100%);
  border-radius: 8px;
  display: flex;
  align-items: center;
  padding: 0 15px;
  color: white;
  font-weight: 600;
  font-size: 13px;
  box-shadow: 0 4px 10px rgba(239, 68, 68, 0.3);
}

.bar-after {
  height: 30px;
  background: linear-gradient(90deg, #10b981 0%, #059669 100%);
  border-radius: 8px;
  display: flex;
  align-items: center;
  padding: 0 15px;
  color: white;
  font-weight: 600;
  font-size: 13px;
  box-shadow: 0 4px 10px rgba(16, 185, 129, 0.3);
}

/* Responsive */
@media (max-width: 1024px) {
  .case-details-hero-banner h1 {
    font-size: 42px;
  }

  .challenge-grid {
    grid-template-columns: 1fr;
  }

  .results-showcase {
    padding: 40px 20px;
    margin-bottom: 0;
  }
}

@media (max-width: 768px) {
  .case-details-hero-banner {
    height: auto;
    padding: 60px 0;
  }

  .case-details-container,
  .case-details-content-wrapper {
    padding: 0 20px;
  }

  .case-details-content-wrapper2 {
    padding: 40px 20px 0;
  }

  .case-details-hero-banner h1 {
    font-size: 32px;
    margin-bottom: 10px;
  }

  .case-details-hero-stats {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    margin-top: 14px;
  }

  .case-details-stat-label {
    font-size: 12px;
    letter-spacing: 0;
    line-height: normal;
  }

  .case-details-client-card {
    padding: 20px;
    margin-top: -50px;
  }

  .case-details-client-header {
    flex-direction: column;
    text-align: center;
  }

  .case-details-stat-number {
    font-size: 24px;
  }

  .section h2,
  .case-details-client-info h2,
  .case-details-section h2,
  .results-header h2 {
    font-size: 24px;
  }

  .strategy-cards {
    grid-template-columns: 1fr;
  }

  .chart-row {
    flex-direction: column;
    align-items: flex-start;
  }

  .case-details-hero-banner p,
  .fact-box .value,
  .case-details-meta-item i {
    font-size: 14px;
    line-height: 1.6;
  }

  .case-details-client-logo {
    width: 80px;
    height: 80px;
  }

  .case-details-client-meta {
    gap: 10px;
  }

  .fact-box .label {
    font-size: 12px;
    letter-spacing: 0;
    margin-bottom: 6px !important;
  }

  .fact-box {
    padding: 20px;
  }

  .section-intro {
    font-size: 14px;
  }

  .challenge-visual {
    padding: 20px 12px;
  }

  .speed-score {
    font-size: 32px;
  }

  .challenge-list {
    padding: 0px;
    background: none;
    box-shadow: none;
  }

  .strategy-card {
    padding: 20px;
    box-shadow: -16px -3px 12px 1px rgba(0, 0, 0, 0.08);
  }

  .strategy-cards {
    gap: 20px;
    margin-top: 20px;
  }

  .strategy-card h3 {
    font-size: 18px;
  }

  .strategy-card li {
    padding: 2px 0;
    padding-left: 30px;
    font-size: 14px;
  }

  .case-details-section {
    margin-bottom: 0;
  }

  .results-header p {
    font-size: 14px;
    color: white;
  }

  .results-header {
    margin-bottom: 20px;
  }

  .metric-before {
    font-size: 14px;
  }

  .metric-after {
    font-size: 18px;
  }

  .improvement-badge {
    font-size: 10px;
    padding: 6px 12px;
  }

  .metric-card {
    padding: 20px 10px;
    flex: 1;
  }

  .metric-comparison {
    gap: 4px;
    margin-bottom: 4px;
  }

  .metrics-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 20px;
  }

  .case-details-metric-label {
    margin-bottom: 0;
    font-size: 12px;
  }
  .challenge-grid {
    padding-bottom: 50px;
  }
}
