.ttcl-collapsible-container {
  padding: 70px 20px;
  background-color: #f5f5f5;
}

.ttcl-collapsible-wrapper {
  max-width: 1400px;
  margin: 0 auto;
}

.ttcl-collapsible-header {
  text-align: center;
  margin-bottom: 50px;
}

.ttcl-collapsible-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1a1a1a;
  margin: 0 0 10px 0;
  line-height: 1.2;
}

.ttcl-collapsible-subtitle {
  font-size: 2rem;
  font-weight: 600;
  color: #1a1a1a;
  margin: 0 0 30px 0;
}

.ttcl-collapsible-description {
  font-size: 1rem;
  color: #333;
  line-height: 1.6;
  max-width: 700px;
  margin: 0 auto;
}

.ttcl-collapsible-description-intro {
  margin-bottom: 5px;
}

.ttcl-collapsible-description-approach {
  margin: 0;
}

.ttcl-collapsible-sections {
  display: flex;
  flex-direction: column;
  gap: 0;
}

.ttcl-collapsible-section {
  /* background-color: #fff; */
  border-bottom: 1px solid #2563eb;
  transition: all 0.3s ease;
}

.ttcl-collapsible-section:first-child {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.ttcl-collapsible-section:last-child {
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  border-bottom: none;
}

.ttcl-collapsible-section-header {
  display: flex;
  align-items: center;
  padding: 25px 30px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.ttcl-collapsible-section.ttcl-collapsible-expanded {
  background-color: #ecf2ff;
}

.ttcl-collapsible-section-number {
  font-size: 2rem;
  font-weight: 700;
  color: #1a1a1a;
  margin-right: 30px;
  min-width: 40px;
}

.ttcl-collapsible-section-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1a1a1a;
  flex: 1;
  margin: 0;
}

.ttcl-collapsible-section-icon {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease;
}

.ttcl-collapsible-section.ttcl-collapsible-expanded
  .ttcl-collapsible-section-icon {
  transform: rotate(180deg);
}

.ttcl-collapsible-section-icon svg {
  width: 100%;
  height: 100%;
  stroke: #666;
  stroke-width: 2;
  fill: none;
}

.ttcl-collapsible-section-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease, padding 0.3s ease;
  padding: 0 30px 0 100px;
}

.ttcl-collapsible-section.ttcl-collapsible-expanded
  .ttcl-collapsible-section-content {
  max-height: 300px;
  padding: 0 30px 25px 100px;
}

.ttcl-collapsible-section-text {
  font-size: 1rem;
  color: #333;
  line-height: 1.6;
  margin: 0;
}

@media (max-width: 768px) {
  .ttcl-collapsible-container {
    padding: 40px 15px;
  }

  .ttcl-collapsible-title {
    font-size: 1.75rem;
  }

  .ttcl-collapsible-subtitle {
    font-size: 1.5rem;
  }

  .ttcl-collapsible-section-header {
    padding: 20px 20px;
  }

  .ttcl-collapsible-section-number {
    font-size: 1.5rem;
    margin-right: 20px;
    min-width: 30px;
  }

  .ttcl-collapsible-section-title {
    font-size: 1.1rem;
  }

  .ttcl-collapsible-section-content {
    padding: 0 20px 0 70px;
  }

  .ttcl-collapsible-section.ttcl-collapsible-expanded
    .ttcl-collapsible-section-content {
    padding: 0 20px 20px 70px;
  }
}
