/* Hero Section */
/* Hero Section */
.case-study-page .case-hero-section {
  position: relative;
  min-height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding: 80px 20px;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
}

section.case-hero-section:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="rgba%28255,255,255,0.05%29" stroke-width="0.5"/></pattern></defs><rect width="100" height="100" fill="url%28%23grid%29"/></svg>');
  opacity: 0.8;
}

.case-study-page .case-mouse-follower {
  position: absolute;
  width: 256px;
  height: 256px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  filter: blur(60px);
  pointer-events: none;
  transition: all 0.3s ease-out;
  transform: translate(-50%, -50%);
}

.case-study-page .case-bg-overlay {
  position: absolute;
  inset: 0;
  overflow: hidden;
  pointer-events: none;
}

.case-study-page .case-bg-circle-1 {
  position: absolute;
  top: -50%;
  left: -50%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 50%;
  filter: blur(60px);
  animation: pulse 4s ease-in-out infinite;
}

.case-study-page .case-bg-circle-2 {
  position: absolute;
  bottom: -50%;
  right: -50%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 50%;
  filter: blur(60px);
  animation: pulse 4s ease-in-out infinite;
  animation-delay: 1s;
}

