  position: absolute;
  inset: 0;
  opacity: 0.1;
  background-image: radial-gradient(circle, white 1px, transparent 1px);
  background-size: 30px 30px;
} */

.case-study-page .case-hero-content {
  position: relative;
  z-index: 10;
  max-width: 1280px;
  margin: 0 auto;
  text-align: center;
}

.case-study-page .case-hero-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border-radius: 50px;
  padding: 10px 24px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  width: fit-content;
  margin: auto;
  margin-bottom: 20px;
}

.case-study-page .case-badge-icon {
  width: 16px;
  height: 16px;
  color: white;
}

.case-study-page .case-badge-text {
  color: white;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 2px;
}

.case-study-page .case-hero-title {
  font-size: 64px;
  font-weight: 900;
  color: white;
  margin-bottom: 24px;
  line-height: 1.2;
  letter-spacing: 0.5px;
}

.case-study-page .case-gradient-text {
  background: linear-gradient(to right, #6c93eb, #93c5fd, #4c82f9);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.case-study-page .case-hero-description {
  font-size: 20px;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
  max-width: 768px;
  margin: 0 auto;
  line-height: 1.5;
}

.case-study-page .case-hero-subdescription {
  font-size: 20px;
  color: rgb(255, 255, 255);
  max-width: 672px;
  margin: 0 auto 20px;
}

.case-study-page .case-cta-buttons {
  display: flex;
  flex-direction: column;
  gap: 16px;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
}

.case-study-page .case-btn-primary {
  padding: 16px 32px;
  background: white;
  color: #1d4ed8;
  border-radius: 50px;
  font-weight: 700;
  font-size: 18px;
  border: none;
  cursor: pointer;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}

.case-study-page .case-btn-primary:hover {
  transform: scale(1.05);
  box-shadow: 0 25px 60px rgba(0, 0, 0, 0.4);
}

.case-study-page .case-btn-secondary {
  padding: 16px 32px;
  background: white;
  backdrop-filter: blur(10px);
  color: #333333;
  border-radius: 8px;
  font-weight: 600;
  font-size: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: all 0.3s ease;
}

.case-study-page .case-btn-secondary:hover {
  background: #2563eb;
  color: white;
  border-color: #2563eb;
}

.case-study-page .case-stats-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 32px;
  max-width: 768px;
  margin: 0 auto;
  /* background: white; */
  border: 1px dashed white;
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 8px;
}

.case-study-page .case-stat-item {
  text-align: center;
}

.case-study-page .case-stat-number {
  font-size: 30px;
  font-weight: 900;
  color: white;
  /* margin-bottom: 8px; */
  line-height: 1.2;
}

.case-study-page .case-stat-label {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
}

@keyframes pulse {
  0%,
  100% {
    opacity: 0.3;
  }

  50% {
    opacity: 0.6;
  }
}

@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-20px);
  }
}

/* Responsive */
@media (min-width: 640px) {
  .case-study-page .case-cta-buttons {
    flex-direction: row;
  }
}

@media (max-width: 768px) {
  .case-study-page .case-hero-section {
  padding: 40px 20px;
}
  .case-study-page .case-hero-title {
    font-size: 56px;
  }

  .case-study-page .case-hero-description {
    font-size: 16px;
  }

  .case-study-page .case-hero-subdescription {
    font-size: 16px;
  }

  .case-study-page .case-stat-number {
    font-size: 24px;
  }

  .case-study-page .case-stat-label {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .case-study-page .case-hero-title {
    font-size: 42px;
  }

  .case-study-page .case-stats-container {
    gap: 16px;
    margin-top: 20px !important;
  }

}

