* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  line-height: 1.6;
  color: #1a1a1a;
  background: #ffffff;
  overflow-x: clip !important;
}
html,
body {
  overflow-x: hidden;
}
/* Header Styles */
.header {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding: 15px 0;
  transition: all 0.3s ease;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
}

.header.scrolled {
  background: #ffffff;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0px;
}
.header .logo img {
  max-width: 200px;
  height: 100%;
  width: 100%;
}
.header .nav-container {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.header .logo {
  font-size: 28px;
  font-weight: 700;
  color: #5c27fe;
}

.header .nav-menu {
  display: flex;
  list-style: none;
  gap: 30px;
}

.header .nav-menu a {
  text-decoration: none;
  color: #1a1a1a;
  font-weight: 500;
  transition: color 0.3s ease;
}

.header .nav-menu a:hover {
  color: #5c27fe;
}
.header .nav-menu a.active {
  color: #5c27fe;
}

.header .cta-button {
  background: #2563eb;
  color: white;
  padding: 12px 24px;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  transition: all 5s ease;
}

.header .cta-button:hover {
  background: linear-gradient(135deg, #1e3c72 0%, #6a0dad 100%);
}
.header .button_wrapper {
  text-align: center;
  margin-top: 20px;
}
/* Container */
.container {
  max-width: 1400px;
  margin: 0 auto;
  /* padding: 0 20px; */
}

