.menu_drawer {
  position: fixed;
  top: 90px;
  left: -100%;
  width: 100%;
  height: 100vh;
  background: #fff;
  transition: left 0.3s ease-in-out;
  z-index: 999;
  display: flex;
  border-top: 1px solid #ddd;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  visibility: hidden;
}

.menu_drawer.open {
