/* Global Css */
.blur-vanish {
  opacity: 0;
  filter: blur(10px);
  transform: translateY(50px) scale(0.9);
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.blur-vanish.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0) scale(1);
}

.blur-vanish-left {
  opacity: 0;
  filter: blur(8px);
  transform: translateX(-60px) rotate(-2deg);
  transition: all 1s cubic-bezier(0.23, 1, 0.32, 1);
}

.blur-vanish-left.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateX(0) rotate(0deg);
}

.blur-vanish-right {
  opacity: 0;
  filter: blur(8px);
  transform: translateX(60px) rotate(2deg);
  transition: all 1s cubic-bezier(0.23, 1, 0.32, 1);
}

.blur-vanish-right.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateX(0) rotate(0deg);
}

.blur-vanish-scale {
  opacity: 0;
  filter: blur(12px);
  transform: scale(0.7) rotateY(20deg);
  transition: all 1.2s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.blur-vanish-scale.visible {
  opacity: 1;
  filter: blur(0px);
  transform: scale(1) rotateY(0deg);
}

/* Footer Styles */
.footer {
  background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
  color: white;
  padding: 80px 0 0;
  position: relative;
  overflow: hidden;
  border-top: 0.5px dashed white;
}

.footer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="rgba(255,255,255,0.05)" stroke-width="0.5"/></pattern></defs><rect width="100" height="100" fill="url(%23grid)"/></svg>');
  opacity: 0.3;
}

.footer-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0;
  position: relative;
  z-index: 1;
}

@media (max-width: 1400px) {
  .footer-container {
    padding: 0 20px;
  }
}

.footer-main {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 40px;
  margin-bottom: 40px;
}

.footer-brand {
  grid-column: span 1;
}

.footer-logo {
  width: 100%;
  height: fit-content;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 20px;
  transition: transform 0.3s ease;
}

.footer-logo:hover {
  transform: translateY(-2px);
}

.footer-logo svg {
  width: 30px;
  height: 30px;
  fill: white;
}

.footer-brand h3 {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 12px;
  background: linear-gradient(135deg, #ffffff, #e3f2fd);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.footer-brand p {
  color: rgba(255, 255, 255, 1);
  line-height: 1.6;
  font-size: 14px;
  margin-bottom: 8px;
}

.footer-menu {
  grid-column: span 2;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.footer-menu h4 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 20px;
  color: #ffffff;
  position: relative;
}

.footer-menu h4::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 45px;
  height: 2px;
  background: linear-gradient(90deg, #2563eb, #2563eb);
  border-radius: 1px;
}

.footer-menu ul {
  list-style: none;
}

.footer-menu li {
  margin-bottom: 12px;
}

.footer-menu a {
  color: rgba(255, 255, 255, 1);
  text-decoration: none;
  font-size: 16px;
  font-weight: 400;
  transition: all 0.3s ease;
  display: inline-block;
  position: relative;
}

.footer-menu a:hover {
  color: #ffffff;
  transform: translateX(5px);
}

.footer-menu a::before {
  content: "";
  position: absolute;
  top: 50%;
  left: -15px;
  width: 6px;
  height: 6px;
  background: #fff;
  border-radius: 50%;
  transform: translateY(-50%) scale(0);
  transition: transform 0.3s ease;
}

.footer-menu a:hover::before {
  transform: translateY(-50%) scale(1);
}

.footer-newsletter {
  grid-column: span 1;
}

.footer-newsletter h4 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 20px;
  color: #ffffff;
  position: relative;
}

.footer-newsletter h4::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 45px;
  height: 2px;
  background: linear-gradient(90deg, #2563eb, #2563eb);
  border-radius: 1px;
}

.footer-newsletter p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
  line-height: 1.6;
  margin-bottom: 20px;
}

.newsletter-form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 15px;
}

.newsletter-input {
  padding: 12px 16px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 14px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.newsletter-input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.newsletter-input:focus {
  outline: none;
  border-color: #ff6b6b;
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 20px rgba(255, 107, 107, 0.3);
}

.newsletter-btn {
  background: #2563eb;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgb(37 99 235);
}

.newsletter-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgb(37 99 235);
}

.footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding: 30px 0;
  text-align: center;
}

.footer-bottom p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  margin: 0;
}

.footer-bottom a {
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-bottom a:hover {
  color: #ff8e8e;
}

.footer-bottom a {
  padding-left: 5px;
}
@media (max-width: 768px) {
  .footer-main {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .footer-container {
    padding: 0 20px;
  }

  .footer-brand,
  .footer-menu,
  .footer-newsletter {
    grid-column: span 1;
  }

  .footer-menu {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .footer-container {
    padding: 020;
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 40px 0 0;
  }

  .footer-brand h3 {
    font-size: 20px;
  }

  .footer-menu h4,
  .footer-newsletter h4 {
    font-size: 16px;
  }
}
