.blog-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 80px 20px;
}

.cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
}

.cards-grid a {
  text-decoration: none;
}

.section-title {
  color: #333;
}

.blog-blogs-card {
  background: white;
  border-radius: 10px;
  overflow: visible;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  display: flex;
  margin-bottom: 10px;
  flex-direction: column;
  overflow: hidden;
  border: 1px solid #e0e0e0;
}

/* .blog-blogs-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
} */

.card-inner {
  /* border-radius: 10px; */
  /* overflow: hidden; */
  flex: 1;
}

.card-image-wrapper {
  position: relative;
  width: 100%;
  height: 280px;
  overflow: hidden;
}

.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.6s ease;
}

.blog-blogs-card:hover .card-image {
  transform: scale(1.1);
}

.blog-image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.9);
  opacity: 0;
  transition: opacity 0.4s ease;
}

.blog-blogs-card:hover .blog-image-overlay {
  opacity: 1;
}

.date-badge {
  position: absolute;
  top: 20px;
  right: 20px;
  background: #2563eb;
  padding: 10px 14px;
  /* border-radius: 10px; */
  text-align: center;
  box-shadow: 0 4px 12px rgba(110, 193, 252, 0.4);
  z-index: 10;
  line-height: 1.2;
}

.date-day {
  font-size: 14px;
  font-weight: 700;
  color: #fff;
  display: block;
  line-height: 1;
}

.date-month {
  font-size: 14px;
  font-weight: 600;
  color: #fff;
  text-transform: uppercase;
  display: block;
  margin-top: 2px;
}

.blog-card-content {
  padding: 28px 30px 24px;
}

.card-meta {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 16px;
  font-size: 14px;
}

.meta-item {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #999;
  font-size: 14px;
  line-height: normal;
}

.meta-item svg {
  width: 20px;
  height: 20px;
  color: #2563eb;
}

.meta-icon {
  width: 16px;
  height: 16px;
  color: #2563eb;
}

.blog-card-title {
  font-size: 22px;
  font-weight: 700;
  color: #222;
  line-height: 1.4;
  margin-bottom: 0;
  transition: color 0.3s ease;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.blog-blogs-card:hover .blog-card-title {
  color: #2563eb;
}

.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px 28px;
  background: white;
}

.read-more {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #333;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: color 0.3s ease, gap 0.3s ease;
}

.blog-card-wrapper:hover .read-more {
  color: #2563eb;
  gap: 12px;
}

.arrow-icon {
  width: 18px;
  height: 18px;
  transition: transform 0.3s ease;
}

.blog-blogs-card:hover .arrow-icon {
  transform: translateX(4px);
}

.blog-card-comments {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #999;
  font-size: 14px;
}

.comment-icon {
  width: 20px;
  height: 20px;
}

.blog-card-wrapper:hover .blog-card-comments {
  color: #2563eb;
}

@media (max-width: 768px) {
  .cards-grid {
    grid-template-columns: 1fr;
  }

  .blog-card-title {
    font-size: 18px;
  }

  .blog-container {
    padding: 40px 20px;
  }

  .blog-card-content {
    padding: 20px 15px;
  }
}

/* blog details css */
.blog-details-container {
  max-width: 1000px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 30px;
  padding: 70px 0;
}

/* .blog-details-main-content {
  flex: 1;
  max-width: 660px;
} */

/* .blog-details-sidebar {
  width: 320px;
  flex-shrink: 0;
} */

.blog-details-hero-image {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 30px;
}

.blog-details-main-title {
  font-size: 32px;
  font-weight: 700;
  color: #333333;
  line-height: 1.3;
  margin-bottom: 20px;
}

.blog-details-description {
  font-size: 16px;
  color: #333;
  line-height: 1.6;
  margin-bottom: 30px;
}

.blog-details-quote-box {
  background-color: #ecf2ff;
  border-left: 4px solid #2563eb;
  border-radius: 10px;
  padding: 30px 35px;
  margin: 35px 0;
  position: relative;
}

.blog-details-quote-text {
  font-size: 16px;
  color: #444;
  line-height: 1.4;
  margin-bottom: 12px;
  font-style: italic;
}

.blog-details-quote-author {
  font-size: 16px;
  color: #888;
  font-style: italic;
}

.blog-details-quote-mark {
  position: absolute;
  right: 30px;
  bottom: 24px;
  color: #2563eb;
  line-height: 1;
  width: 50px;
  height: 50px;
}

.blog-details-section-title {
  font-size: 24px;
  font-weight: 700;
  color: #333333;
  margin: 40px 0 16px;
  line-height: 1.4;
}
.blog-details-section-title2 {
  font-size: 32px;
  font-weight: 700;
  color: #333333;
  margin: 20px 0 16px;
}

.two-column-images {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin: 30px 0;
}

.column-image {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 8px;
}

.info-list {
  list-style: none;
  /* margin: 30px 0; */
}

.info-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 15px;
  font-size: 16px;
  color: #333333;
  line-height: 1.4;
}

.bullet-icon {
  width: 20px;
  height: 20px;
  background-color: #2563eb;
  border-radius: 50%;
  flex-shrink: 0;
  margin-top: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bullet-check {
  color: white;
  font-size: 12px;
  font-weight: bold;
}

.post-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 40px 0;
  padding: 25px 0;
  border-top: 1px solid #e5e5e5;
  /* border-bottom: 1px solid #e5e5e5; */
}

.post-meta {
  display: flex;
  align-items: center;
  gap: 15px;
  font-size: 14px;
  color: #666;
}

.meta-label {
  font-weight: 700;
  color: #333333;
  font-size: 16px;
}

.meta-badge {
  background-color: #2563eb;
  color: white;
  padding: 12px 16px;
  border-radius: 6px;
  font-size: 13px;
  font-weight: 600;
  border: none;
  cursor: pointer;
}
.meta-badge2 {
  background-color: transparent;
  color: #333333;
  padding: 12px 16px;
  border-radius: 6px;
  font-size: 13px;
  font-weight: 600;
  border: 1px solid #2563eb;
  cursor: pointer;
  transition: all 0.3s ease;
}

.meta-badge2:hover {
  background-color: #2563eb;
  border-color: #2563eb;
  color: white;
}

.comment-section {
  margin-top: 50px;
}

.comment-title {
  font-size: 24px;
  font-weight: 700;
  color: #333333;
  margin-bottom: 15px;
}

.comment-subtitle {
  font-size: 16px;
  color: #333333;
  margin-bottom: 25px;
}

.comment-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-textarea {
  width: 100%;
  min-height: 200px;
  padding: 15px;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  font-family: inherit;
  resize: vertical;
  background-color: #f8f6fa;
}

.form-textarea:focus,
.blog-details-form-input:focus,
.form-textarea:focus-visible,
.blog-details-form-input:focus-visible {
  border: 1.5px solid #2563eb !important;
  outline: none;
}

.blog-details-form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.blog-details-form-input {
  width: 100%;
  padding: 22px;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  background-color: #f8f6fa;
}

.blog-details-form-checkbox {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 16px;
  color: #aaa;
  /* margin: 10px 0; */
  cursor: pointer;
}

input[type="checkbox"] {
  width: 16px;
  height: 16px;
}

.blog-details-submit-btn {
  border: none;
  font-size: 16px;
  border-radius: 6px;
  cursor: pointer;
}

.blog-details-submit-btn:hover {
  background-color: #e5a512;
}

.blog-details-search-box {
  background-color: #eeeeee;
  border-radius: 8px;
  padding: 8px;
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 30px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  /* border: 1px solid #dbdbdb59; */
}

.blog-details-search-input {
  flex: 1;
  border: none;
  padding: 10px;
  font-size: 16px;
  outline: none;
  background: #eeeeee;
}

.blog-details-search-btn {
  background-color: #2563eb;
  border: none;
  width: 45px;
  height: 45px;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  flex-shrink: 0;
  transition: all 0.3s ease;
}

.blog-details-search-btn:hover {
  background-color: black;
}

.blog-details-sidebar-section {
  background-color: #f1f1f1;
  border-radius: 8px;
  padding: 35px;
  margin-bottom: 25px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  /* border: 1px solid #dbdbdb59; */
}

.blog-details-sidebar-title,
.blog-details-social-title {
  font-size: 24px;
  font-weight: 700;
  color: #333333;
  margin-bottom: 25px;
}

.news-item {
  display: flex;
  gap: 15px;
  margin-bottom: 25px;
  align-items: center;
  cursor: pointer;
}

.news-item:last-child {
  margin-bottom: 0;
}

.news-image {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  object-fit: cover;
  flex-shrink: 0;
}

.news-content {
  flex: 1;
}

.news-title {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
  line-height: 1.4;
  margin-bottom: 6px;
  transition: all 0.3s ease;
}

.news-title:hover {
  color: #2563eb;
}

.blog-details-search-icon {
  transition: all 0.3s ease;
}

.news-date {
  font-size: 14px;
  color: #999;
}

.blog-details-category-list {
  list-style: none;
}

.blog-details-category-item {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  padding: 10px 0;
  /* border-bottom: 1px solid #dbdbdb59; */
  font-size: 16px;
  color: #333333;
  font-weight: 400;
  cursor: pointer;
  transition: all 0.3s ease;
}

li.blog-details-category-item span {
  line-height: 1.4;
}

li.blog-details-category-item:first-child {
  padding-top: 0;
}

.blog-details-category-item:last-child {
  border-bottom: none;
}

.blog-details-category-item:hover {
  color: #2563eb;
}

.blog-details-category-icon {
  width: 16px;
  height: 16px;
  color: #999;
}

.tag-cloud {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.blog-details-tag-item {
  padding: 8px 18px;
  background-color: #f8f9fa;
  border: 1px solid #e5e5e5;
  border-radius: 6px;
  font-size: 13px;
  color: #666;
  cursor: pointer;
  transition: all 0.3s;
}

.blog-details-tag-item svg {
  display: flex;
}

.blog-details-tag-item:hover {
  background-color: #2563eb;
  color: white;
  border-color: #2563eb;
}

.social-instagram:hover {
  background: #e30b5c;
  border-color: #e30b5c;
}
.social-linkedin:hover {
  background: #0077b5;
  border-color: #0077b5;
}
.social-twitter:hover {
  background: #000;
  border-color: #000;
}
.social-reddit:hover {
  background: #ff5700;
  border-color: #ff5700;
}
.social-threads:hover {
  background: #231f20;
  border-color: #231f20;
}

@media (max-width: 968px) {
  .blog-details-container {
    grid-template-columns: 1fr;
    padding: 40px 20px;
    gap: 60px;
  }

  .blog-details-hero-image {
    height: 200px;
  }

  .blog-details-main-title {
    font-size: 24px;
  }

  .blog-details-description,
  .blog-details-quote-text,
  .blog-details-quote-author,
  .info-item,
  .comment-subtitle,
  .form-textarea,
  .blog-details-form-input {
    font-size: 14px;
  }

  .blog-details-quote-box {
    padding: 20px 15px;
  }

  .blog-details-quote-mark {
    width: 40px;
    height: 40px;
    right: 18px;
    bottom: 16px;
  }

  h2.blog-details-section-title {
    line-height: 1.4;
  }

  .two-column-images {
    grid-template-columns: 1fr;
  }

  .column-image {
    height: 200px;
  }

  .blog-details-section-title2 {
    font-size: 24px;
    line-height: 1.4;
  }

  .bullet-icon {
    width: 16px;
    height: 16px;
    margin-top: 5px;
  }

  .post-meta {
    flex-direction: column;
    gap: 0;
  }

  .meta-badge,
  .meta-badge2 {
    padding: 10px 14px;
    font-size: 10px;
  }

  .comment-title {
    font-size: 22px;
    margin-bottom: 10px;
  }

  .blog-details-form-input,
  .blog-details-form-checkbox,
  .blog-details-search-input,
  li.blog-details-category-item {
    font-size: 14px;
  }

  .blog-details-form-checkbox {
    display: block;
  }

  input[type="checkbox"] {
    width: 14px;
    height: 14px;
    margin-right: 8px;
  }

  .blog-details-sidebar {
    width: 100%;
  }

  .blog-details-form-row {
    grid-template-columns: 1fr;
  }

  .blog-details-sidebar-section {
    padding: 25px 20px;
  }

  .news-title,
  .news-date {
    font-size: 13px;
  }

  .blog-details-tag-item {
    padding: 8px 30px;
  }

  .news-image {
    width: 60px;
    height: 60px;
  }

  .blog-details-social {
    padding: 8px 18px;
  }

  .blog-social-icon {
    width: 18px;
  }
}
