/* About Page Mission */
/* Mission Section */
.mission {
  padding: 80px 0;
}

.mission-content {
  display: flex;
  align-items: center;
  gap: 60px;
}

.mission-left {
  flex: 1;
}

.mission-right {
  flex: 1;
  text-align: center;
}

.mission h2 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 20px;
  color: #1a1a1a;
}

.mission p {
  font-size: 16px;
  color: #6b7280;
  line-height: 1.6;
}

.mission-image {
  width: 100%;
  height: 100%;
  /* background: linear-gradient(135deg, #27AE60, #5C27FE); */

  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
}
.mission_element {
  margin-top: 15px;
  font-size: 14px;
  color: #5c27fe;
  font-weight: 600;
}
/* Who We Work With */
.clients {
  padding: 80px 0;
  background: #f9fafb;
}

.section-title {
  text-align: center;
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 50px;
  color: #1a1a1a;
}

.clients-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
}

.client-card {
  text-align: center;
  padding: 30px 20px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.client-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(92, 39, 254, 0.15);
}

.client-icon {
  font-size: 48px;
  margin-bottom: 20px;
  color: #5c27fe;
}

.client-card h3 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 10px;
}

.client-card p {
  font-size: 14px;
  color: #6b7280;
}

/* Team Section */
.team {
  padding: 80px 0;
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}

.team-card {
  background: white;
  border-radius: 12px;
  padding: 30px;
  text-align: center;
  border: 1px solid #e5e7eb;
  transition: all 0.3s ease;
}

.team-card:hover {
  border-color: #5c27fe;
  transform: translateY(-5px) scale(1.02);
  box-shadow: 0 15px 35px rgba(92, 39, 254, 0.1);
}

.team-avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: linear-gradient(135deg, #5c27fe, #27ae60);
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  color: white;
}

.team-card h3 {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 10px;
}

.team-card p {
  color: #6b7280;
  font-size: 14px;
}

/* Vision Section */
.vision {
  padding: 80px 0;
  background: #f9fafb;
}

.vision-content {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 50px;
}

.vision h2 {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 30px;
}

.vision-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 30px;
}

.vision-item {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.vision-item:hover {
  transform: translateY(-3px);
}

.vision-item .icon {
  font-size: 24px;
  color: #27ae60;
  margin-bottom: 15px;
}

.vision-item h4 {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 10px;
}

.vision-item p {
  font-size: 14px;
  color: #6b7280;
}

/* Why Choose Us */
.why-choose {
  padding: 80px 0;
}

.why-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
}

.why-card:hover {
  border-color: #5c27fe;
  transform: translateY(-5px);
}

.why-icon {
  width: 60px;
  height: 60px;
  border-radius: 12px;
  background: linear-gradient(135deg, #5c27fe, #27ae60);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: white;
  flex-shrink: 0;
}

.why-text h3 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 10px;
}

.why-text p {
  font-size: 14px;
  color: #6b7280;
  line-height: 1.5;
}

/* CTA Section */
.cta-section {
  padding: 80px 0;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  text-align: center;
  color: white;
}

.cta-section h2 {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #fff;
  opacity: 1;
  filter: blur(0);
}

.cta-section p {
  font-size: 16px;
  margin-bottom: 40px;
  opacity: 1;
  filter: blur(0px);
  transform: translate(0);
}

.calendly-container {
  background: white;
  border-radius: 12px;
  padding: 40px;
  margin-top: 40px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.calendly-placeholder {
  height: 300px;
  background: #f3f4f6;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #6b7280;
  font-weight: 600;
  border: 2px dashed #d1d5db;
}

.stagger-item {
  opacity: 0;
  filter: blur(6px);
  transform: translateY(40px);
  transition: all 0.6s ease-out;
}

.stagger-item.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0);
}

.stagger-item:nth-child(1) {
  transition-delay: 0.1s;
}
.stagger-item:nth-child(2) {
  transition-delay: 0.2s;
}
.stagger-item:nth-child(3) {
  transition-delay: 0.3s;
}
.stagger-item:nth-child(4) {
  transition-delay: 0.4s;
}
.stagger-item:nth-child(5) {
  transition-delay: 0.5s;
}
.stagger-item:nth-child(6) {
  transition-delay: 0.6s;
}

.blur-glow {
  opacity: 0;
  filter: blur(15px) brightness(0.3);
  transform: scale(0.8);
  transition: all 1.5s cubic-bezier(0.19, 1, 0.22, 1);
}

.blur-glow.visible {
  opacity: 1;
  filter: blur(0px) brightness(1);
  transform: scale(1);
}

.floating-blur {
  opacity: 0;
  filter: blur(20px);
  transform: translateY(80px) rotateX(30deg);
  transition: all 1.8s cubic-bezier(0.23, 1, 0.32, 1);
}
.mission-image img {
  width: 100%;
}
.floating-blur.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0) rotateX(0deg);
}

.text-blur-reveal {
  opacity: 0;
  filter: blur(4px);
  transform: translateY(20px);
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.text-blur-reveal.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0);
}

.enhanced-hover {
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.enhanced-hover:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 20px 40px rgba(92, 39, 254, 0.15);
  filter: drop-shadow(0 8px 16px rgba(0, 0, 0, 0.1));
}
.about_hero {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 20px;
}
.button_wrapper_about {
  display: flex;
  justify-content: flex-start;
}
.about_page {
  padding-top: 80px !important;
  padding-bottom: 80px !important;
}
.button_wrapper_about a {
  margin-top: 0px;
}
/* Responsive Design */
@media (max-width: 768px) {
  .nav-menu {
    display: none;
  }

  .hero.about_page .container {
    padding: 0 20px;
  }

  .hero.about_page {
    padding: 60px 0 !important;
  }

  .about_hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }
  .about_hero .hero-image {
    transform: none !important;
    margin-top: 0px;
    aspect-ratio: 1;
    height: 100%;
  }
  .mission {
    padding: 20px 0;
  }
  .clients {
    padding: 20px 0;
  }
  .team {
    padding: 20px 0;
  }
  .vision {
    padding: 20px 0;
  }
  .why-choose {
    padding: 20px 0;
  }
  .cta-section {
    padding: 20px 0;
  }
  .section-title {
    font-size: 24px;
    line-height: 36px;
  }

  .about_page {
    padding: 30px 0 30px !important;
  }

  .hero h1 {
    font-size: 28px;
  }

  .mission-content {
    flex-direction: column;
    text-align: center;
  }

  .clients-grid,
  .team-grid,
  .vision-grid,
  .why-content {
    grid-template-columns: 1fr;
  }

  .why-card {
    flex-direction: column;
    text-align: center;
  }
}

/* Progress Bar Animation */
.progress-bar {
  width: 100%;
  height: 6px;
  background: #e5e7eb;
  border-radius: 3px;
  overflow: hidden;
  margin: 20px 0;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #5c27fe, #27ae60);
  border-radius: 3px;
  width: 0%;
  transition: width 2s ease-out;
}

.progress-fill.animate {
  width: 75%;
}

