/* Floating Button */
.galaxy-wrapper {
  width: 100%;
  max-width: 600px;
  aspect-ratio: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.galaxy-container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border-radius: 20px;
}

/* Orbital paths */
.orbit {
  position: absolute;
  border: 2px solid #ffffff26;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: rotateOrbit 30s linear infinite;
}

.orbit-1 {
  width: 46.67%; /* 280px / 600px */
  height: 46.67%;
  aspect-ratio: 1;
}

.orbit-2 {
  width: 75%; /* 400px / 600px */
  height: 75%;
  aspect-ratio: 1;
}

.orbit-3 {
  width: 100%; /* 520px / 600px */
  height: 100%;
  aspect-ratio: 1;
}

@keyframes rotateOrbit {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

/* Center sun/logo */
.center-logo {
  position: relative;
  z-index: 10;
  width: 24%;
  aspect-ratio: 1;
  background: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  padding: 5px;
}

.center-logo-image {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.center-logo-image img {
  width: 100%;
  height: 100%;
  padding: 16px;
  object-fit: contain;
  animation: pulseIn 2s ease-in-out infinite;
}

@keyframes pulseIn {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

/* Orbiting planets - fixed on each orbit */
.planet {
  position: absolute;
  width: 14%; /* 70px / 600px */
  aspect-ratio: 1;
  background: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

.planet .service-logos {
  width: 50%;
  height: 50%;
}

/* Position planets at different positions maintaining desktop layout */
.planet-1 {
  left: 22.83%; /* 143px / 600px */
  top: 4.17%; /* 25px / 600px */
}

.planet-2 {
  left: 86.83%;
  top: 43%;
}

.planet-3 {
  left: 68.83%;
  top: 78.17%;
}

.planet-4 {
  left: -1.17%; /* -4px / 600px */
  top: 43%; /* 224px / 600px */
}

.planet-5 {
  left: 69.33%; /* 356px / 600px */
  top: 8.17%; /* 31px / 600px */
}

.planet-6 {
  left: 22%;
  bottom: 5%; /* Keep as percentage */
}

.planet:hover {
  transform: scale(1.15);
}

/* Social media colors */
.facebook svg {
