.step {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 2;
}

.step-icon {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  background: white;
  border: 3px solid #f8f8f8;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  position: relative;
  z-index: 3;
}

.step-icon svg {
  width: 35px;
  height: 35px;
  stroke: #000;
  stroke-width: 3;
  fill: none;
}

