  font-size: 24px;
  color: #000;
  margin-bottom: 12px;
  font-weight: 600;
}

.step-description {
  font-size: 16px;
  color: #666;
  line-height: 1.5;
  max-width: 280px;
}

.line-container {
  position: absolute;
  top: 45px;
  left: 0;
  right: 0;
  height: 4px;
  display: flex;
  align-items: center;
  z-index: 1;
  padding: 0 calc((100% / 8) + 35px);
}

.line-segment {
  flex: 1;
  height: 4px;
  background-image: repeating-linear-gradient(
    to right,
    #e0e0e0 0px,
    #e0e0e0 10px,
    transparent 10px,
    transparent 20px
  );
  position: relative;
  overflow: hidden;
}

.line-segment:not(:last-child) {
  margin-right: 0;
}

.line-fill {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-image: repeating-linear-gradient(
    to right,
    #2563eb 3px,
    #2563eb 14px,
    transparent 14px,
    transparent 17px
  );
  width: 0%;
  transition: width 1s ease-in-out;
}

.line-segment.animate .line-fill {
  width: 100%;
}
.line-segment:nth-child(1) .line-fill {
  transition-delay: 0.5s;
}
.line-segment:nth-child(2) .line-fill {
  transition-delay: 1.2s;
}

.line-segment:nth-child(3) .line-fill {
  transition-delay: 1.8s;
}

