  .step::after {
    content: "";
    position: absolute;
    top: 70px;
    left: 50%;
    transform: translateX(-50%);
    width: 4px;
    height: 0;
    background-image: repeating-linear-gradient(
      to bottom,
      #3d5a47 0px,
      #3d5a47 10px,
      transparent 10px,
      transparent 20px
    );
    transition: height 0.6s ease-in-out;
  }

  .step:last-child::after {
    display: none;
  }

  .step.animate::after {
    height: 50px;
  }

  .step:nth-child(2).animate::after {
    transition-delay: 0.3s;
  }

  .step:nth-child(3).animate::after {
    transition-delay: 0.6s;
  }

  .step-description {
    max-width: 100%;
  }
}

