  align-items: center;
  /* gap: 60px; */
  position: relative;
}

.testimonial-content {
  flex: 1;
  /* max-width: 550px; */
}

.testimonial-label {
  color: #2563eb;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 20px;
  display: block;
}

.testimonial-heading {
  font-size: 42px;
  font-weight: 700;
  color: #1a1a1a;
  line-height: 1.2;
  margin-bottom: 40px;
}

.testimonial-quote-icon {
  width: 60px;
  height: 50px;
  margin-bottom: 25px;
}

.testimonial-quote-icon img {
  width: 100%;
}

.testimonial-text {
  font-size: 16px;
  line-height: 1.8;
  color: #666;
  margin-bottom: 30px;
}

/* .testimonial-author {
  margin-bottom: 35px;
} */

.testimonial-author-name {
  font-size: 20px;
  line-height: 20px;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 5px;
}

.testimonial-author-title {
  font-size: 14px;
  color: #333;
  font-weight: 400;
}

.testimonial-avatars {
  display: flex;
  align-items: center;
  gap: 15px;
  position: relative;
}

.testimonial-avatars {
  margin-top: 20px;
}

.testimonial-headshot {
  display: flex;
  gap: 10px;
}

.testimonial-avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  overflow: hidden;
  border: 3px solid #fff;
  cursor: pointer;
  transition: transform 0.3s ease, border-color 0.3s ease;
  position: relative;
}

.testimonial-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.testimonial-avatar.active {
  border-color: #2563eb;
  transform: scale(1.1);
}

.testimonial-avatar:hover {
  transform: scale(1.05);
}

.testimonial-nav {
  display: flex;
  gap: 10px;
  margin-left: 20px;
}

.testimonial-nav-btn {
  width: 40px;
  height: 40px;
  border: none;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 2px 10px rgb(0 0 0 / 17%);
}

.testimonial-nav-btn:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(245, 166, 35, 0.3);
}

.testimonial-nav-btn svg {
  width: 16px;
  height: 16px;
  fill: #333;
  transition: fill 0.3s ease;
}

.testimonial-nav-btn:hover svg {
  fill: #fff;
}

.testimonial-image-section {
  flex: 1;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.testimonial-image-container {
  position: relative;
  width: 450px;
  height: 550px;
}

/* .testimonial-image-bg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 350px;
        height: 450px;
        background: linear-gradient(135deg, #ffeaa7 0%, #fdcb6e 100%);
        border-radius: 50% 50% 50% 50% / 60% 60% 40% 40%;
        z-index: 1;
      } */

.testimonial-image-person {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 400px;
  height: 500px;
  z-index: 2;
  background-size: contain;
  display: contents;
}
.testimonial-image-person img {
  width: 100%;
}

.testimonial-social-icons {
  position: absolute;
  top: 50px;
  right: -30px;
  z-index: 3;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.testimonial-social-icon {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: float 3s ease-in-out infinite;
}

.testimonial-social-icon:nth-child(1) {
  background-color: #25d366;
  animation-delay: 0s;
}

.testimonial-social-icon:nth-child(2) {
  background-color: #1da1f2;
  animation-delay: 0.5s;
}

.testimonial-social-icon:nth-child(3) {
  background-color: #e4405f;
  animation-delay: 1s;
}

.testimonial-social-icon:nth-child(4) {
  background-color: #f5a623;
  animation-delay: 1.5s;
}

.testimonial-social-icon:nth-child(5) {
  background-color: #00b4ab;
  animation-delay: 2s;
}

.testimonial-social-icon:nth-child(6) {
  background-color: #ff6b6b;
  animation-delay: 2.5s;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

.testimonial-decoration {
  position: absolute;
  bottom: 11%;
  left: 5%;
  width: 200px;
  height: 200px;
  opacity: 0.6;
  z-index: 1;
}

.testimonial-decoration {
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

/* Optional: Add a subtle rotation for more dynamic effect */
.testimonial-decoration {
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(5deg);
  }
}

.testimonial-decoration img {
  width: 110px;
  height: 110px;
}

