@media (max-width: 968px) {
  .testimonial-container {
    flex-direction: column-reverse;
    text-align: center;
    gap: 20px;
  }

  .testimonial-social-icons {
    top: 10px;
    right: 0px;
    gap: 6px;
  }

  .testimonial-social-icon {
    width: 20px;
    height: 20px;
  }

  .testimonial-content {
    max-width: 100%;
  }

  .testimonial-heading {
    font-size: 24px;
    text-align: left;
    margin-bottom: 20px;
  }

  .testimonial-text {
    font-size: 14px !important;
    text-align: left;
  }

  .testimonial-avatars {
    justify-content: center;
    flex-direction: column;
  }

  .testimonial-image-section {
    margin-top: 0;
  }

  .testimonial-image-container {
    width: 100%;
    height: fit-content;
  }

  .testimonial-decoration {
    display: none;
  }

  .tech-element1dpe9 > .tech-elementngsxb {
    padding-block-end: 50px;
    padding-block-start: 50px;
    text-align: var(--text-align);
  }

  section.testimonial__section .container {
    padding: 0 20px !important;
  }

  .testimonial-label {
    text-align: left;
  }

  section.testimonial__section {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  section.ourapproch-section {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .testimonial-nav {
    margin-left: 0;
    margin-top: 20px;
  }
}

