.privpol-container-wrapper {
  padding: 70px 0;
}

.privpol-content-box {
  max-width: 1000px;
  margin: 0 auto;
}

.privpol-header-section {
  background: linear-gradient(135deg, #2563eb 0%, #764ba2 100%);
  padding: 50px 40px;
  color: white;
  text-align: center;
}

.privpol-body-content {
  /* padding: 50px 40px; */
  color: #333333;
  line-height: 1.6;
}

.privpol-intro-text {
  font-size: 18px;
  color: #333333;
  margin-bottom: 40px;
  line-height: 1.7;
}

.privpol-section-block {
  margin-bottom: 40px;
}

.privpol-section-heading {
  font-size: 24px;
  font-weight: 600;
  color: #1a202c;
  margin: 0 0 16px 0;
  padding-bottom: 10px;
  border-bottom: 2px solid #e2e8f0;
}

.privpol-paragraph-text {
  font-size: 16px;
  color: #333333;
  margin: 0 0 16px 0;
  line-height: 1.7;
}

.privpol-bullet-list {
  margin: 16px 0;
  padding-left: 30px;
}

.privpol-list-item {
  font-size: 16px;
  color: #333333;
  margin-bottom: 10px;
  line-height: 1.7;
}

.privpol-contact-box {
  background: #ecf2ff;
  border-left: 4px solid #2563eb;
  padding: 24px;
  border-radius: 8px;
  margin-top: 40px;
}

.privpol-contact-title {
  font-size: 20px;
  font-weight: 600;
  color: #1a202c;
  margin: 0 0 12px 0;
}

.privpol-contact-text {
  font-size: 16px;
  color: #333333;
  margin: 0 0 8px 0;
  line-height: 1.7;
}

.privpol-email-link {
  color: #2563eb;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.privpol-email-link:hover {
  color: #764ba2;
  text-decoration: underline;
}

@media (max-width: 768px) {
  .privpol-container-wrapper {
    padding: 50px 10px;
  }

  .privpol-header-section {
    padding: 40px 25px;
  }

  .privpol-main-title {
    font-size: 32px;
  }

  .privpol-body-content {
    padding: 0;
  }

  .privpol-section-heading {
    font-size: 22px;
  }

  .privpol-paragraph-text,
  .privpol-list-item,
  .privpol-contact-text,
  .privpol-intro-text {
    font-size: 14px;
    line-height: 1.6;
  }
}
