:root {
    --primary-blue: #2563eb;
    --light-blue: #eff6ff;
    --dark-gray: #1f2937;
    --mid-gray: #4b5563;
    --light-gray: #f9fafb;
    --border-color: #e5e7eb;
    --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -2px rgba(0, 0, 0, 0.1);
    --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -4px rgba(0, 0, 0, 0.1);
}

body {
    margin: 0;
    padding: 0;
    background-color: #fff;
    color: var(--dark-gray);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.careers-container {
    max-width: 1400px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 32px;
    padding-right: 32px;
}

.icon {
    margin-left: 0.5rem;
    height: 1.25rem;
    width: 1.25rem;
}

.icon-small {
    height: 1rem;
    width: 1rem;
    margin-right: 0.25rem;
}

/* --- Header --- */
.header-bar {
    padding: 1rem 0;
    border-bottom: 1px solid var(--border-color);
    position: sticky;
    top: 0;
    background-color: #fff;
    z-index: 10;
}

.header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo-text {
    font-size: 1.5rem;
    font-weight: 800;
    color: var(--primary-blue);
    letter-spacing: -0.05em;
}

.login-button {
    padding: 0.5rem 1rem;
    font-size: 0.875rem;
    font-weight: 600;
    border-radius: 0.5rem;
    background-color: var(--dark-gray);
    color: white;
    transition: background-color 0.3s;
}

.login-button:hover {
    background-color: #374151;
}

/* --- Hero Section --- */
.hero-section {
    padding-top: 3rem;
    padding-bottom: 3rem;
    display: grid;
    gap: 3rem;
    align-items: center;
}

@media (min-width: 1024px) {
    .hero-section {
        grid-template-columns: repeat(2, 1fr);
        padding-top: 2rem;
        padding-bottom: 2rem;
    }
}

.hero-text-content {
    padding-right: 2.5rem;
    /* lg:pr-10 */
}

h2.tagline {
    font-size: 28px;
    color: var(--primary-blue);
    text-transform: uppercase;
    letter-spacing: 0.05em;
    margin-bottom: 0.5rem;
}

.hero-title {
    font-size: 36px;
    /* 4xl */
    font-weight: 800;
    color: var(--dark-gray);
    line-height: 1.2;
    margin-bottom: 1.5rem;
}

@media (min-width: 768px) {
    .hero-title {
        font-size: 3rem;
        /* 6xl */
    }
}

.highlight-text {
    color: var(--primary-blue);
}

.hero-description {
    font-size: 16px;
    /* xl */
    color: var(--mid-gray);
    margin-bottom: 2rem;
}

.cta-button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0.75rem 2rem;
    border: 1px solid transparent;
    font-size: 1rem;
    font-weight: 500;
    border-radius: 0.75rem;
    box-shadow: var(--shadow-lg);
    color: white;
    background-color: var(--primary-blue);
    transition: background-color 0.3s, transform 0.3s;
    text-decoration: none;
}

.cta-button:hover {
    background-color: #1d4ed8;
    transform: scale(1.02);
}

.hero-image-careers-container {
    position: relative;
}

.hero-image {
    width: 100%;
    height: 20rem;
    /* 80 */
    object-fit: cover;
    border-radius: 1rem;
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 8px 10px -6px rgba(0, 0, 0, 0.1);
    margin: 0;
    /* shadow-2xl */
}

.career-hover-card-indicator{
    background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
}

@media (min-width: 768px) {
    .hero-image {
        height: 28rem;
        /* 450px equivalent */
    }
}

.hero-image-overlay {
    position: absolute;
    inset: 0;
    background-color: #1e40af;
    /* Blue-900 */
    opacity: 0.1;
    border-radius: 1rem;
    pointer-events: none;
}

/* --- Section Titles & Base Styling --- */
.section {
    padding-top: 3rem;
    padding-bottom: 3rem;
}

@media (min-width: 768px) {
    .section {
        padding-top: 2rem;
        padding-bottom: 2rem;
    }
}

@media (max-width: 768px) {
    .section {
        padding-top: 2rem;
        padding-bottom: 2rem;
    }

    .hero-section {
        padding-top: 0rem;
        padding-bottom: 2rem;
    }

    h2.tagline {
        font-size: 16px;
    }

    .hero-title {
        font-size: 28px;
        font-weight: 600;
    }

    .hero-text-content {
        padding-right: 0px;
    }

    a.btn-primary.blur-vanish-scale.visible {
        margin: auto;
    }

    .hero-text-content {
        order: 2;
    }

    .hero-section {
        gap: 24px;
    }

    h3.section-main-title {
        font-size: 20px;
    }

    .content-image-layout.reverse-on-desktop {
        gap: 24px;
    }

    h4.content-title {
        font-size: 20px;
    }

    span.faq-question {
        padding: 20px 0px;
    }

    .careers-container {
        padding-left: 10px;
        padding-right: 10px;
    }
}

.section-benefits {
    background-color: var(--light-gray);
}

.section-openings {
    background-color: var(--light-gray);
}

.section-title-careers-container {
    text-align: center;
    margin-bottom: 1rem;
    /* padding-top: 1rem; */
}

.section-main-title {
    font-size: 1.875rem;
    font-weight: 600;
    color: var(--dark-gray);
}

@media (min-width: 768px) {
    .section-main-title {
        font-size: 28px;
        /* 4xl */
    }
}

/* --- Benefits Grid --- */
.benefits-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 24px;
}

@media (min-width: 768px) {
    .benefits-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 1024px) {
    .benefits-grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

.benefit-card {
    padding: 1.5rem;
    background-color: white;
    border-radius: 8px;
    box-shadow: var(--shadow-md);
    border: 1px solid var(--border-color);
    transition: box-shadow 0.3s, transform 0.3s;
    position: relative;
    overflow: hidden;
}

.benefit-card:hover .shopify-problem-hover-indicator {
    transform: scaleX(1);
}

.benefit-card:hover {
    box-shadow: var(--shadow-lg);
    transform: translateY(-0.25rem);
}

.benefit-icon {
    height: 2.5rem;
    width: 2.5rem;
    color: var(--primary-blue);
    margin-bottom: 1rem;
    background-color: var(--light-blue);
    padding: 0.5rem;
    border-radius: 0.5rem;
}

.card-title {
    font-size: 18px;
    font-weight: 600;
    color: var(--dark-gray);
    margin-bottom: 0.5rem;
}

.card-description {
    color: var(--mid-gray);
    font-size: 1rem;
}

.card-description.small {
    font-size: 0.875rem;
}

/* --- Content Image Layout (Benefits & Values) --- */
.content-image-layout {
    margin-top: 4rem;
    display: grid;
    grid-template-columns: 1fr;
    gap: 2.5rem;
    align-items: center;
}

@media (min-width: 1024px) {
    .content-image-layout {
        grid-template-columns: repeat(2, 1fr);
        gap: 4rem;
    }

    .reverse-on-desktop>.image-wrapper {
        order: 2;
    }

    .reverse-on-desktop>.text-content {
        order: 1;
    }
}

.content-title {
    font-size: 28px;
    font-weight: 600;
    color: var(--dark-gray);
    margin-bottom: 1rem;
}

.content-paragraph {
    font-size: 16px;
    color: var(--mid-gray);
}

.image-wrapper {
    /* Ensures aspect ratio doesn't collapse */
}

.content-image {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 8px ;
    box-shadow: var(--shadow-lg);
}

/* --- Values Grid --- */
.values-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
}

@media (min-width: 768px) {
    .values-grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

.value-item {
    padding: 1.5rem;
    background-color: white;
    border-radius: 0.75rem;
    border: 1px solid var(--border-color);
    text-align: center;
}

@media (min-width: 768px) {
    .value-item {
        padding: 2rem;
    }
}

.value-icon {
    height: 3rem;
    width: 3rem;
    color: var(--primary-blue);
    margin-bottom: 0.75rem;
    margin-left: auto;
    margin-right: auto;
}

/* --- Openings Section --- */
.openings-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 24px;
}

h2.tagline.Current-Openings {
    font-size: 16px;
}

@media (min-width: 768px) {
    .openings-grid {
        grid-template-columns: repeat(2, 1fr);
    }

    h2.tagline {
        font-size: 18px;
    }

}

@media (min-width: 1024px) {
    .openings-grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

.job-card {
    padding: 1.5rem;
    background-color: white;
    border-radius: 8px;
    box-shadow: var(--shadow-sm);
    border: 1px solid var(--border-color);
    transition: box-shadow 0.3s;
}

.job-card:hover {
    box-shadow: var(--shadow-lg);
}

.job-role {
    font-size: 18px;
    font-weight: 700;
    color: var(--dark-gray);
    margin-bottom: 0.25rem;
}

.job-department {
    font-size: 0.875rem;
    color: var(--primary-blue);
    font-weight: 600;
    margin-bottom: 0.75rem;
}

.job-location {
    display: flex;
    align-items: center;
    font-size: 0.875rem;
    color: #6b7280;
    /* gray-500 */
}

.job-details-link {
    margin-top: 1rem;
}

.details-button {
    color: var(--primary-blue);
    transition: color 0.3s;
    font-size: 0.875rem;
    font-weight: 500;
    border: none;
    background: transparent;
    cursor: pointer;
}

.details-button:hover {
    color: #1d4ed8;
}

.application-prompt {
    text-align: center;
    margin-top: 3rem;
    margin-bottom: 1rem;
}

.prompt-text {
    font-size: 1.125rem;
    color: var(--mid-gray);
    margin-bottom: 1rem;
}

button.btn-secondary {
    margin: auto;
    cursor: pointer;
}

.general-application-button {
    display: inline-flex;
    align-items: center;
    padding: 0.5rem 1.5rem;
    border: 1px solid var(--primary-blue);
    font-size: 1rem;
    font-weight: 500;
    border-radius: 0.75rem;
    color: var(--primary-blue);
    background-color: white;
    transition: background-color 0.3s;
    cursor: pointer;
}

.general-application-button:hover {
    background-color: var(--light-blue);
}

/* --- FAQ Section --- */
.faq-list {
    max-width: 768px;
    /* max-w-4xl equivalent */
    margin-left: auto;
    margin-right: auto;
    border-top: 1px solid var(--border-color);
    border-bottom: 1px solid var(--border-color);
    padding-bottom: 1px;
    /* To make sure the last border shows correctly */
}

.faq-item-careers-container {
    border-bottom: 1px solid var(--border-color);
}

.faq-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 10px 0;
    text-align: left;
    border: none;
    background: none;
    cursor: pointer;
}

/* button.faq-button {
    border-bottom: 1px solid #0000001f;
} */


.faq-item-careers-container {
    border-bottom: 1px solid #00000021;
}

.faq-question {
    font-size: 1.125rem;
    font-weight: 600;
    color: var(--dark-gray);
}

.faq-question:hover {
    background-color: transparent;
}



.faq-icon {
    height: 1.5rem;
    width: 1.5rem;
    color: var(--primary-blue);
    transition: transform 0.3s;
}

.faq-icon.rotate {
    transform: rotate(180deg);
}

.faq-answer-content {
    overflow: hidden;
    transition: max-height 0.5s ease-in-out, opacity 0.5s ease-in-out;
    max-height: 0;
    opacity: 0;
    margin-left: 20px;
}

.faq-list>*:last-child {
    border-bottom: none;
}

.faq-answer-content.open {
    max-height: 24rem;
    /* Arbitrarily large enough height for text */
    opacity: 1;
    padding-top: 0.5rem;
    padding-bottom: 1rem;
    margin-left: 20px;
}

.faq-answer-text {
    color: var(--mid-gray);
    padding-right: 2rem;
}

/* --- Footer --- */
.footer-bar {
    background-color: var(--dark-gray);
    padding: 2rem 0;
}

.footer-content {
    text-align: center;
    color: #9ca3af;
    /* gray-400 */
}

.footer-title {
    font-size: 1.25rem;
    font-weight: 700;
    color: white;
    margin-bottom: 0.5rem;
}

.footer-copyright {
    font-size: 0.875rem;
}