section.case-hero-section.how-work-header {
  min-height: 300px;
}

p.case-hero-description.how-we-description {
  margin-bottom: 20px;
}

.hww-main-container {
  color: #333;
  background: #ffffff;
}

.hww-intro-section {
  max-width: 1400px;
  margin: 0 auto;
  padding: 80px 20px 60px;
  text-align: center;
}

.hww-intro-title {
  font-size: 48px;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 20px;
  line-height: 1.2;
}

.hww-intro-subtitle {
  font-size: 16px;
  color: #333;
  max-width: 700px;
  margin: 0 auto 40px;
  line-height: 1.6;
}

.hww-steps-section {
  background: linear-gradient(135deg, #f8f9ff 0%, #ffffff 100%);
  padding: 80px 20px;
}

.hww-steps-container {
  max-width: 1400px;
  margin: 0 auto;
}

.hww-section-header {
  text-align: center;
  margin-bottom: 60px;
}

.hww-section-title {
  font-size: 42px;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 15px;
}
.hww-section-title2 {
  font-size: 42px;
  font-weight: 700;
  color: white;
  margin-bottom: 15px;
}

.hww-section-description {
  font-size: 18px;
  color: #666;
  max-width: 600px;
  margin: 0 auto;
}
.hww-section-description2 {
  font-size: 16px;
  color: white;
  max-width: 600px;
  margin: 0 auto;
}

.hww-timeline-container {
  position: relative;
  padding: 40px 0;
}

.hww-timeline-line {
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 3px;
  background: linear-gradient(135deg, #2563eb57 0%, #1e3c727d 100%);
  transform: translateX(-50%);
}

.hww-step-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 60px;
  position: relative;
}

.hww-step-wrapper:nth-child(even) {
  flex-direction: row-reverse;
}

.hww-step-card {
  width: 45%;
  background: white;
  border-radius: 8px;
  padding: 32px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  cursor: pointer;
}

.hww-step-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 12px 40px rgba(74, 144, 226, 0.25);
}

.hww-step-icon-wrapper {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  height: 80px;
  background: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  transition: all 0.4s ease;
  z-index: 2;
}

.hww-step-wrapper:hover .hww-step-icon-wrapper {
  transform: translate(-50%, -50%) scale(1.15) rotate(360deg);
  box-shadow: 0 6px 30px rgba(74, 144, 226, 0.4);
}

.hww-step-number {
  position: absolute;
  top: -10px;
  right: -10px;
  width: 32px;
  height: 32px;
  background: #2563eb;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 14px;
  box-shadow: 0 2px 10px rgba(74, 144, 226, 0.4);
}

.hww-step-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
}

.hww-step-icon-small {
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  transition: all 0.3s ease;
}

.hww-step-card:hover .hww-step-icon-small {
  transform: scale(1.1) rotate(5deg);
}

.hww-step-title {
  font-size: 24px;
  font-weight: 700;
  color: #1a1a1a;
  margin: 0;
  flex: 1;
}

.hww-step-duration {
  background: #e8f2ff;
  color: #2563eb;
  padding: 6px 16px;
  border-radius: 20px;
  font-size: 13px;
  font-weight: 600;
}

.hww-step-description {
  color: #333;
  line-height: 1.7;
  font-size: 16px;
  margin: 0;
}

.hww-features-section {
  max-width: 100%;
  margin: 0 auto;
  padding: 80px 20px;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  overflow: hidden;
  position: relative;
}

.hww-features-section:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="rgba%28255,255,255,0.05%29" stroke-width="0.5"/></pattern></defs><rect width="100" height="100" fill="url%28%23grid%29"/></svg>');
  opacity: 0.8;
}

.hww-features-wrapper {
  max-width: 1400px;
  margin: 0 auto;
}

.hww-features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  margin-top: 50px;
}

.hww-feature-card {
  background: white;
  border: 2px solid #f0f0f0;
  border-radius: 8px;
  padding: 40px 30px;
  text-align: center;
  transition: all 0.4s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.hww-feature-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: #2563eb;
  transform: scaleX(0);
  transition: transform 0.4s ease;
}

.hww-feature-card:hover::before {
  transform: scaleX(1);
}

.hww-feature-card:hover {
  transform: translateY(-8px);
  border-color: #2563eb;
  box-shadow: 0 12px 40px rgba(74, 144, 226, 0.2);
}

.hww-feature-icon {
  width: 80px;
  height: 80px;
  margin: 0 auto 25px;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  transition: all 0.4s ease;
}

.hww-feature-card:hover .hww-feature-icon {
  transform: scale(1.1) rotate(360deg);
}

.hww-feature-title {
  font-size: 22px;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 8px;
}

.hww-feature-description {
  color: #333;
  line-height: 1.6;
  font-size: 15px;
}

.hww-cta-section {
  background: white;
  padding: 80px 20px;
  text-align: center;
  color: black;
}

.hww-cta-title {
  font-size: 42px;
  font-weight: 700;
  margin-bottom: 20px;
}

.hww-cta-description {
  font-size: 20px;
  margin-bottom: 40px;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  opacity: 0.95;
}

.hww-cta-button {
  background: #2563eb;
  color: white;
  padding: 18px 48px;
  border-radius: 50px;
  font-size: 18px;
  font-weight: 700;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.hww-cta-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.25);
  background: #f8f9ff;
  color: #333;
}

@media (max-width: 768px) {
  .hww-intro-section,
  .hww-steps-section,
  .hww-features-section,
  .hww-cta-section {
    padding: 40px 20px;
  }

  .hww-intro-title,
  .hww-section-title,
  .hww-section-title2 {
    font-size: 28px;
    line-height: 1.6;
  }

  .hww-intro-subtitle,
  .hww-section-description,
  .hww-section-description2,
  .hww-cta-description {
    font-size: 16px;
    line-height: 1.6;
  }

  .hww-step-icon-small {
    width: 34px;
    height: 34px;
    padding: 6px;
  }

  .hww-step-title,
  .hww-feature-title {
    font-size: 18px;
    line-height: 1.2;
  }

  .hww-step-description {
    line-height: 1.6;
    font-size: 14px;
  }

  .hww-step-card {
    padding: 16px;
    width: 83% !important;
    margin-left: 30px !important;
    margin-top: 16px;
  }

  .hww-step-duration {
    font-size: 12px;
  }

  .hww-timeline-line {
    left: 18px;
  }

  .hww-step-wrapper,
  .hww-step-wrapper:nth-child(even) {
    flex-direction: column;
  }

  .hww-step-icon-wrapper {
    left: 18px;
    width: 50px;
    height: 50px;
    padding: 12px;
  }

  .hww-timeline-container {
    padding: 0;
  }

  .hww-step-wrapper {
    margin-bottom: 0;
  }

  .hww-features-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .hww-feature-card {
    padding: 20px 10px;
  }

  .hww-feature-icon {
    width: 50px;
    height: 50px;
    padding: 12px;
  }

  .hww-cta-title {
    font-size: 28px;
    line-height: 1.2;
  }

  .hww-features-grid {
    margin-top: 0;
  }

  .hww-section-header {
    margin-bottom: 30px;
  }
}
