/* New Homepage Hero */
#home.hero {
  /* padding: 20px 0 80px; */
  background: url("https://cdn.shopify.com/s/files/1/0509/2160/3270/files/banner1.png?v=1765181287");
  background-repeat: no-repeat;
  background-size: cover;
  height: 90vh;
  background-position: center left;
  position: relative;
}

#home.hero .hero-content {
  margin: 0;
}

#home .hero-content {
  display: flex;
  align-items: center;
  gap: 20px;
  height: 100% !important;
}

#home .hero-left {
  width: 50%;
}

#home.hero h1 {
  font-size: 36px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 20px;
  line-height: 1.2;
}

section#home.hero {
  padding: 0 !important;
}

#home .hero-quote {
  font-size: 16px;
  color: #5c27fe;
  font-style: italic;
  font-weight: 600;
  margin: 20px 0;
  text-align: left;
  padding: 15px 20px;
  background: rgb(255 255 255 / 82%);
  border-left: 4px solid #5c27fe;
  border-radius: 8px;
}

#home .hero .subheadline {
  font-size: 16px;
  color: #fff;
  margin-bottom: 20px;
  line-height: 1.6;
}

#home .hero-ctas {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

#home .hero-image {
  width: 100%;
  height: 100%;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 18px;
  font-weight: 600;
  transform: none !important;
}

.homepage-hero-section .container {
  height: 100%;
}

/* State */
.stats {
  padding: 80px 0;
  background: #f9fafb;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.stat-item {
  text-align: center;
  padding: 30px 20px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.stat-item:hover {
  transform: translateY(-5px);
}

.stat-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
  font-size: 24px;
  color: white;
}

.stat-icon.purple {
  background: #5c27fe;
}

.stat-icon.green {
  background: #27ae60;
}

.stat-icon.orange {
  background: #ff8c42;
}

.stat-icon.black {
  background: #1a1a1a;
}

.stat-item h3 {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 10px;
}

/* Services Section */
.services {
  padding: 80px 0;
}

.section-title {
  text-align: center;
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 50px;
  color: #1a1a1a;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-bottom: 50px;
}

.service-card {
  padding: 30px;
  background: white;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  transition: all 0.3s ease;
}

.service-card:hover {
  border-color: #5c27fe;
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(92, 39, 254, 0.1);
}

.service-card h3 {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 15px;
  color: #1a1a1a;
}

.service-card p {
  color: #6b7280;
  line-height: 1.6;
}

/* Case Strudies */

.case-studies {
  padding: 80px 0;
  background: #f9fafb;
}

.case-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
}

.case-card {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.case-card:hover {
  transform: translateY(-5px);
}

.case-image {
  height: 200px;
  background: linear-gradient(135deg, #1e3c72 0%, #6a0dad 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
}

.case-content {
  padding: 25px;
}

.case-content h3 {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 15px;
}

h2.blur-glow.visible {
  line-height: 1.4;
}

.case-metrics {
  display: flex;
  gap: 20px;
  margin-top: 15px;
}

.metric {
  text-align: center;
}

.metric-value {
  font-size: 18px;
  font-weight: 700;
  color: #27ae60;
}

.metric-label {
  font-size: 12px;
  color: #6b7280;
}

/* Why Section */
.why-section {
  padding: 80px 0;
}

.why-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 40px;
  margin-bottom: 50px;
}

.why-card {
  text-align: center;
}

.why-icon {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: linear-gradient(135deg, #5c27fe, #27ae60);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
  font-size: 32px;
  color: white;
}

.why-card h3 {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 15px;
}

.why-card p {
  font-size: 16px;
  letter-spacing: 0;
}

/* CTA Section */
.cta-section {
  padding: 80px 0;
  background: #5c27fe;
  text-align: center;
  color: white;
}

.cta-section h2 {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 30px;
}

.cta-buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

.btn-white {
  background: white;
  display: flex;
  align-items: center;
  color: #000;
  padding: 15px 30px;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.btn-white:hover {
  background: #f3f4f6;
  transform: translateY(-2px);
}

.btn-outline {
  background: transparent;
  color: white;
  padding: 15px 30px;
  border: 2px solid white;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.btn-outline:hover {
  background: white;
  color: #000;
}

/* Blog Section */
.blog-section {
  padding: 80px 0;
}

.blog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}

.blog-card {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid #e5e7eb;
  transition: all 0.3s ease;
}

.blog-card:hover {
  border-color: #5c27fe;
  transform: translateY(-5px);
}

.blog-image {
  height: 180px;
  background: linear-gradient(135deg, #ff8c42, #5c27fe);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
}

.blog-content {
  padding: 25px;
}

.blog-content h3 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 10px;
}

.blog-excerpt {
  color: #6b7280;
  font-size: 14px;
  margin-bottom: 15px;
}

.read-more {
  color: #5c27fe;
  text-decoration: none;
  font-weight: 600;
  font-size: 14px;
}

.logo svg {
  width: 200px;
  height: fit-content;
}

/* Responsive Design */
@media (min-width: 768px) {
  .line_break {
    display: none;
  }
}

@media (max-width: 768px) {
  .nav-menu {
    display: none;
  }

  .cta-section {
    padding: 0 !important;
  }

  .blur-glow.visible {
    line-height: 1.4 !important;
    font-size: 28px !important;
  }

  #home .hero-content {
    flex-direction: column-reverse;
  }

  #home .hero-image {
    margin-top: 0px;
    transform: none !important;
    aspect-ratio: 1;
    height: 100%;
  }

  #home .hero-content {
    gap: 0px;
  }

  #home .btn-primary {
    width: 100%;
  }

  #home .btn-secondary {
    margin-top: 0px;
    width: 100%;
  }

  .services .section-title {
    margin-bottom: 30px !important;
  }

  .hero-content {
    flex-direction: column;
    text-align: center;
  }

  .services .section-title {
    margin-bottom: 40px;
  }

  .hero h1 {
    font-size: 28px;
  }

  .stats-grid,
  .services-grid,
  .case-grid,
  .why-grid,
  .blog-grid {
    grid-template-columns: 1fr;
  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }

  .hero-ctas {
    justify-content: center;
  }
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 1;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in-up {
  animation: fadeInUp 0.8s ease-out;
}

/* Staggered Animation for Grid Items */
.stagger-item {
  opacity: 1;
  filter: blur(6px);
  transform: translateY(40px);
  transition: all 0.6s ease-out;
}

.stagger-item.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0);
}

.stagger-item:nth-child(1) {
  transition-delay: 0.1s;
}

.stagger-item:nth-child(2) {
  transition-delay: 0.2s;
}

.stagger-item:nth-child(3) {
  transition-delay: 0.3s;
}

.stagger-item:nth-child(4) {
  transition-delay: 0.4s;
}

.stagger-item:nth-child(5) {
  transition-delay: 0.5s;
}

.stagger-item:nth-child(6) {
  transition-delay: 0.6s;
}

/* Special Effects */
.blur-glow {
  opacity: 1;
  filter: blur(15px) brightness(0.3);
  transform: scale(0.8);
  transition: all 1.5s cubic-bezier(0.19, 1, 0.22, 1);
}

.blur-glow.visible {
  opacity: 1;
  filter: blur(0px) brightness(1);
  transform: scale(1);
}

/* Floating Effect for Images */
.floating-blur {
  opacity: 1;
  filter: blur(20px);
  transform: translateY(80px) rotateX(30deg);
  transition: all 1.8s cubic-bezier(0.23, 1, 0.32, 1);
}

.floating-blur.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0) rotateX(0deg);
}

/* Text Reveal Effect */
.text-blur-reveal {
  opacity: 1;
  filter: blur(4px);
  transform: translateY(20px);
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.text-blur-reveal.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0);
}

/* Card Hover Enhancement with Blur */
.enhanced-hover {
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.enhanced-hover:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 20px 40px rgba(92, 39, 254, 0.15);
  filter: drop-shadow(0 8px 16px rgba(0, 0, 0, 0.1));
}

