/* Contct Pages */
/* Hero Section */
.hero {
  padding: 80px 0 80px;
  background: linear-gradient(135deg, #f9fafb 0%, #ffffff 100%);
}

section.hero.contact_hero_section .hero-content {
  margin-bottom: 70px;
}

section.hero.contact_hero_section p.subheadline {
  color: #333333 !important;
  text-align: center;
}

.hero-content {
  display: grid;
  grid-template-columns: 1fr;
  gap: 60px;
  align-items: center;
}

.hero-left h1 {
  font-size: 36px;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 20px;
  line-height: 1.2;
}

.hero-left .subheadline {
  font-size: 18px;
  color: #333333;
  margin-bottom: 30px;
  line-height: 1.6;
}

.cta-buttons {
  display: flex;
  gap: 20px;
  margin-bottom: 40px;
}

.contact_hero_section .cta-buttons {
  justify-content: center;
}

section.hero.contact_hero_section {
  padding-bottom: 0;
}

/* Contact Form */
.contact-form {
  background: white;
  padding: 40px;
  border-radius: 16px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  border: 1px solid #e5e7eb;
}

.form-group {
  margin-bottom: 25px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #1a1a1a;
  font-size: 14px;
  text-align: left;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  font-size: 14px;
  transition: all 0.3s ease;
  background: #fafafa;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #5c27fe;
  background: white;
  box-shadow: 0 0 0 3px rgba(92, 39, 254, 0.1);
}

.form-group textarea {
  height: 120px;
  resize: vertical;
}

.submit-btn {
  width: 100%;
  background: linear-gradient(135deg, #5c27fe, #27ae60);
  color: white;
  padding: 15px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(92, 39, 254, 0.3);
}

.submit-btn:active {
  transform: translateY(0);
}

/* Contact Info */
.contact-info {
  padding: 80px 0;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
}

.info-card {
  text-align: center;
  padding: 30px;
  background: white;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  transition: all 0.3s ease;
}

.info-card:hover {
  border-color: #5c27fe;
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(92, 39, 254, 0.1);
}

.info-icon {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: linear-gradient(135deg, #5c27fe, #27ae60);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
  font-size: 28px;
  color: white;
}

.info-card h3 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 10px;
  color: #1a1a1a;
}

.info-card p {
  color: #6b7280;
  font-size: 14px;
  line-height: 1.5;
}

/* Calendly Section */
.calendly-section {
  padding: 80px 0;
  background: #f9fafb;
}

.calendly-content {
  text-align: center;
  margin-bottom: 50px;
}

.calendly-content h2 {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #1a1a1a;
}

.calendly-content p {
  font-size: 16px;
  color: #6b7280;
  max-width: 600px;
  margin: 0 auto;
}

.calendly-container {
  background: white;
  border-radius: 16px;
  padding: 40px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  max-width: 100%;
  margin: 0 auto;
}

.calendly-placeholder {
  height: 600px;
  background: linear-gradient(135deg, #f3f4f6, #e5e7eb);
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #6b7280;
  font-weight: 600;
  border: 2px dashed #d1d5db;
  text-align: center;
}

.calendly-placeholder i {
  font-size: 48px;
  margin-bottom: 20px;
  color: #5c27fe;
}

/* Trust Section */
.trust-section {
  padding: 80px 0;
}

.trust-content {
  text-align: center;
}

.trust-content h2 {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #1a1a1a;
}

.trust-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 40px;
  margin-top: 40px;
}

.trust-item {
  text-align: center;
  padding: 20px;
}

.trust-item .stat-number {
  font-size: 36px;
  font-weight: 700;
  color: #5c27fe;
  margin-bottom: 10px;
  display: block;
}

.trust-item .stat-label {
  font-size: 14px;
  color: #6b7280;
  font-weight: 500;
}

/* Final CTA */
.final-cta {
  padding: 80px 0;
  background: linear-gradient(135deg, #1a1a1a 0%, #2d3748 100%);
  color: white;
  text-align: center;
}

.final-cta h2 {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 20px;
}

.final-cta p {
  font-size: 16px;
  margin-bottom: 40px;
  opacity: 0.9;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.stagger-item {
  opacity: 0;
  filter: blur(6px);
  transform: translateY(40px);
  transition: all 0.6s ease-out;
}

.stagger-item.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0);
}

.stagger-item:nth-child(1) {
  transition-delay: 0.1s;
}
.stagger-item:nth-child(2) {
  transition-delay: 0.2s;
}
.stagger-item:nth-child(3) {
  transition-delay: 0.3s;
}
.stagger-item:nth-child(4) {
  transition-delay: 0.4s;
}

.text-blur-reveal {
  opacity: 0;
  filter: blur(4px);
  transform: translateY(20px);
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.text-blur-reveal.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0);
}

.enhanced-hover {
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.enhanced-hover:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 20px 40px rgba(92, 39, 254, 0.15);
  filter: drop-shadow(0 8px 16px rgba(0, 0, 0, 0.1));
}

/* Form Success/Error States */
.form-message {
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
  font-weight: 500;
  text-align: center;
  display: none;
}

.form-message.success {
  background: #d1fae5;
  color: #065f46;
  border: 1px solid #10b981;
}

.form-message.error {
  background: #fee2e2;
  color: #991b1b;
  border: 1px solid #ef4444;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .hero-content {
    margin: 0;
  }

  .nav-menu {
    display: none;
  }
  .contact_hero_section {
    padding: 0px 0 0px !important;
  }
  .contact-info {
    padding: 20px 0 20px !important;
  }
  .calendly-section {
    padding: 20px 0 20px !important;
  }
  .trust-section {
    padding: 20px 0 20px !important;
  }
  .final-cta {
    padding: 20px 0 20px !important;
  }
  .contact_hero_section .cta-buttons a {
    width: 100%;
    margin-top: 0px;
    justify-content: center;
  }
  .contact_hero_section .hero-content {
    gap: 20px;
  }
  .trust-grid {
    gap: 0px !important;
  }

  .hero-content {
    grid-template-columns: 1fr;
    gap: 40px;
    text-align: center;
  }

  .hero-left h1 {
    font-size: 24px;
  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
    margin-bottom: 0;
  }

  .contact-form {
    padding: 30px 20px;
  }

  .info-grid,
  .trust-grid {
    grid-template-columns: 1fr 1fr;
  }

  .calendly-container {
    padding: 20px;
  }
  .final-cta p {
    margin-bottom: 0px;
  }

  .calendly-placeholder {
    height: 400px;
  }
  p.subheadline {
    color: black !important;
  }
  section.hero.contact_hero_section .hero-content {
    margin-bottom: 0px;
  }
  .hero-content {
    margin: 0px 0 30px 0;
  }
}

/* Loading Animation */
.loading {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: #fff;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Floating elements */
.floating {
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

/* Pulse animation for CTA buttons */
.pulse {
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(92, 39, 254, 0.7);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(92, 39, 254, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(92, 39, 254, 0);
  }
}
.calendly-inline-widget {
  min-width: 100%;
  height: 100%;
}
.calendly-inline-widget iframe {
  width: 100%;
  height: 100%;
}
