.ttcl-comparison-container {
  width: 100%;
  /* background-color: #f8f8f8; */
  padding: 80px 20px;
}

.ttcl-comparison-wrapper {
  max-width: 1400px;
  margin: 0 auto;
  border-radius: 8px;
}

.ttcl-comparison-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1a1a1a;
  text-align: center;
  margin: 0 0 60px 0;
}

.ttcl-comparison-table {
  display: flex;
  flex-direction: column;
  border-radius: 12px;
  overflow: hidden;
}

.ttcl-comparison-header {
  display: grid;
  grid-template-columns: 1fr 200px 250px;
  border-bottom: 2px solid #e0e0e0;
}

.ttcl-comparison-header-cell {
  padding: 25px 30px;
  font-size: 1.25rem;
  font-weight: 600;
  text-align: center;
}

.ttcl-comparison-header-cell:first-child {
  visibility: hidden;
}

.ttcl-comparison-header-ttcl {
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  color: #fff;
  border-radius: 8px 8px 0 0;
  font-size: 1.5rem;
  text-transform: uppercase;
}

.ttcl-comparison-header-traditional {
  /* background-color: #f8f8f8; */
  color: #64748b;
  font-size: 1.3rem;
}

.ttcl-comparison-row {
  display: grid;
  grid-template-columns: 1fr 200px 250px;
  border-bottom: 1px solid #e0e0e0;
  transition: background-color 0.2s ease;
}

.ttcl-comparison-row:last-child {
  border-bottom: none;
}

.ttcl-comparison-row:hover {
  background-color: #fafafa;
}

.ttcl-comparison-feature {
  padding: 25px 30px;
  font-size: 1.05rem;
  font-weight: 600;
  color: #333333;
  display: flex;
  align-items: center;
}

.ttcl-comparison-cell {
  padding: 25px 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ttcl-comparison-cell-ttcl {
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
}

.ttcl-comparison-cell-traditional {
  background-color: #fff;
}

.ttcl-comparison-icon-check {
  width: 32px;
  height: 32px;
  background-color: #cbffdb;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ttcl-comparison-icon-check svg {
  width: 24px;
  height: 24px;
  stroke: #10b981;
  stroke-width: 4;
  fill: none;
}

.ttcl-comparison-icon-cross {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffe0e0;
  border-radius: 50px;
}

.ttcl-comparison-icon-cross svg {
  width: 24px;
  height: 24px;
  stroke: #ef4444;
  stroke-width: 4;
  fill: none;
}

@media (max-width: 1024px) {
  .ttcl-comparison-container {
    padding: 60px 15px;
  }

  .ttcl-comparison-title {
    font-size: 2rem;
    margin-bottom: 40px;
  }

  .ttcl-comparison-header {
    grid-template-columns: 1fr 160px 160px;
  }

  .ttcl-comparison-row {
    grid-template-columns: 1fr 160px 160px;
  }

  .ttcl-comparison-header-cell {
    padding: 20px 15px;
    font-size: 1.1rem;
  }

  .ttcl-comparison-feature {
    padding: 20px 20px;
    font-size: 0.95rem;
  }

  .ttcl-comparison-cell {
    padding: 20px 15px;
  }
}

@media (max-width: 768px) {
  .ttcl-comparison-container {
    padding: 40px 15px;
  }

  .ttcl-comparison-title {
    font-size: 1.75rem;
    margin-bottom: 30px;
  }

  .ttcl-comparison-header {
    grid-template-columns: 1fr 120px 120px;
  }

  .ttcl-comparison-row {
    grid-template-columns: 1fr 120px 120px;
  }

  .ttcl-comparison-header-cell {
    padding: 15px 10px;
    font-size: 0.95rem;
  }

  .ttcl-comparison-feature {
    padding: 18px 15px;
    font-size: 0.9rem;
  }

  .ttcl-comparison-cell {
    padding: 18px 10px;
  }

  .ttcl-comparison-icon-check,
  .ttcl-comparison-icon-cross {
    width: 28px;
    height: 28px;
  }

  .ttcl-comparison-icon-check svg {
    width: 16px;
    height: 16px;
  }

  .ttcl-comparison-icon-cross svg {
    width: 20px;
    height: 20px;
  }
}

@media (max-width: 480px) {
  .ttcl-comparison-title {
    font-size: 1.5rem;
  }

  .ttcl-comparison-header {
    grid-template-columns: 1fr 100px 100px;
  }

  .ttcl-comparison-row {
    grid-template-columns: 1fr 100px 100px;
  }

  .ttcl-comparison-header-cell {
    padding: 12px 8px;
    font-size: 0.85rem;
  }

  .ttcl-comparison-feature {
    padding: 15px 12px;
    font-size: 0.85rem;
  }

  .ttcl-comparison-cell {
    padding: 15px 8px;
  }

  .ttcl-comparison-icon-check,
  .ttcl-comparison-icon-cross {
    width: 24px;
    height: 24px;
  }

  .ttcl-comparison-icon-check svg {
    width: 14px;
    height: 14px;
  }

  .ttcl-comparison-icon-cross svg {
    width: 18px;
    height: 18px;
  }
}
