.brand_marquee_main_section {
  background: #ffffff;
  padding: 20px;
  overflow: hidden;
}

/* .brand_marquee_header_container {
  text-align: center;
  margin-bottom: 60px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
} */

.brand_marquee_subtitle_text {
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 2px;
  color: #667eea;
  margin: 0 0 12px 0;
  text-transform: uppercase;
}

.brand_marquee_title_heading {
  font-size: 36px;
  font-weight: 700;
  color: #1a202c;
  margin: 0;
  line-height: 1.2;
}

/* Marquee Slider - Left to Right */
.brand_marquee_slider_wrapper {
  position: relative;
  width: 100%;
  overflow: hidden;
  /* margin-bottom: 40px; */
  /* padding: 30px 0; */
}

.brand_marquee_track_container {
  display: flex;
  width: fit-content;
  animation: brand_marquee_scroll_animation 30s linear infinite;
}

.brand_marquee_logos_group {
  display: flex;
  align-items: center;
  gap: 60px;
  padding-right: 60px;
}

.brand_marquee_logo_item {
  height: 60px;
  min-width: 190px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 25px;
  background: #f7fafc;
  border-radius: 8px;
  transition: all 0.3s ease;
  cursor: pointer;
  border: 1px solid #e2e8f0;
}

.brand_marquee_logo_item:hover {
  background: #ffffff;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  /* transform: translateY(-3px); */
}

.brand_marquee_logo_image {
  height: 100%;
  width: auto;
  max-width: 100%;
  object-fit: contain;
  filter: grayscale(100%);
  opacity: 0.7;
  transition: all 0.3s ease;
}

.brand_marquee_logo_item:hover .brand_marquee_logo_image {
  filter: grayscale(0%);
  opacity: 1;
}

@keyframes brand_marquee_scroll_animation {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

/* Pause animation on hover */
.brand_marquee_slider_wrapper:hover .brand_marquee_track_container {
  animation-play-state: paused;
}

/* Reverse Direction Marquee - Right to Left */
.brand_marquee_slider_wrapper_reverse {
  position: relative;
  width: 100%;
  overflow: hidden;
  padding: 0 0 20px 0;
}

.brand_marquee_track_container_reverse {
  display: flex;
  width: fit-content;
  animation: brand_marquee_scroll_reverse_animation 35s linear infinite;
}

.brand_marquee_logos_group_reverse {
  display: flex;
  align-items: center;
  gap: 40px;
  padding-right: 40px;
}

@keyframes brand_marquee_scroll_reverse_animation {
  0% {
    transform: translateX(-50%);
  }
  100% {
    transform: translateX(0);
  }
}

.brand_marquee_slider_wrapper_reverse:hover
  .brand_marquee_track_container_reverse {
  animation-play-state: paused;
}

/* Responsive Design */
@media (max-width: 768px) {
  .brand_marquee_main_section {
    padding: 20px 0;
  }

  .brand_marquee_title_heading {
    font-size: 28px;
  }

  /* .brand_marquee_header_container {
    margin-bottom: 40px;
  } */

  .brand_marquee_logos_group,
  .brand_marquee_logos_group_reverse {
    gap: 40px;
    padding-right: 40px;
  }

  .brand_marquee_logo_item {
    min-width: 150px;
    height: 50px;
    padding: 12px 20px;
  }

  .brand_marquee_track_container {
    animation: brand_marquee_scroll_animation 20s linear infinite;
  }

  .brand_marquee_track_container_reverse {
    animation: brand_marquee_scroll_reverse_animation 25s linear infinite;
  }

  .brand_marquee_slider_wrapper {
    padding: 0 0 20px 0;
  }
}

@media (max-width: 480px) {
  .brand_marquee_title_heading {
    font-size: 24px;
  }

  .brand_marquee_subtitle_text {
    font-size: 10px;
  }

  .brand_marquee_logos_group,
  .brand_marquee_logos_group_reverse {
    gap: 20px;
    padding-right: 20px;
  }

  .brand_marquee_logo_item {
    min-width: 150px;
    height: 50px;
    padding: 10px 15px;
  }
}
