/* Service Pages Styles */

/* Hero Section */
.hero {
  padding: 80px 0 80px;
  background: linear-gradient(135deg, #f9fafb 0%, #ffffff 100%);
  overflow: hidden;
}

.hero-content {
  display: flex;
  align-items: center;
  gap: 60px;
}

.hero-left {
  flex: 1;
}

.hero h1 {
  font-size: 42px;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 20px;
  line-height: 1.2;
}

.hero .subheadline {
  font-family: "Inter", sans-serif;
  font-size: 18px;
  color: #6b7280;
  margin-bottom: 40px;
  line-height: 1.6;
  text-align: left;
}

.hero-buttons {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.hero-right {
  flex: 1;
  text-align: center;
}

.hero-image {
  width: 100%;
  height: 100%;
  margin-top: 0px;
  background: none;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 18px;
  font-weight: 600;
  position: relative;
  overflow: hidden;
}

.hero-image::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    45deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  animation: shimmer 3s infinite;
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%) translateY(-100%) rotate(45deg);
  }
  100% {
    transform: translateX(100%) translateY(100%) rotate(45deg);
  }
}

/* Core Services Section */
.core-services {
  padding: 80px 0;
}

.core-services .section-title {
  text-align: center;
  font-size: 42px;
  font-weight: 700;
  letter-spacing: 0px;
  margin-bottom: 0px;
  color: #1a1a1a;
}

.core-services .section-subtitle {
  text-align: center;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #6b7280;
  margin-bottom: 50px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
}

.service-card {
  background: white;
  border-radius: 12px;
  padding: 30px;
  border: 1px solid #e5e7eb;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.service-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(92, 39, 254, 0.05),
    transparent
  );
  transition: left 0.6s ease;
}

.service-card:hover::before {
  left: 100%;
}

.service-card:hover {
  border-color: #5c27fe;
  transform: none;
  box-shadow: 0 20px 40px rgba(92, 39, 254, 0.15);
}
.portfoliobtn_wrap a {
  width: fit-content;
}
.portfoliobtn_wrap {
  display: flex;
  justify-content: center;
}
.service-icon {
  width: 60px;
  height: 60px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: white;
  margin-bottom: 20px;
}

a.case-story-link {
  text-decoration: none;
}

@media (max-width: 768px) {
  .credibility-slide {
    text-align: center;
  }
  .credibility-icon {
    width: 40px;
    height: 40px;
    margin: 0 auto 10px !important;
  }

  .service-new {
    width: 40px;
    height: 40px;
    margin: 0 0 20px !important;
  }

  .service-icon img {
    width: 40px !important;
  }

  .service-card h3 {
    font-size: 18px;
    line-height: 24px;
  }
  .credibility-item-title {
    font-size: 18px;
    margin-bottom: 0px;
  }
}

.service-card h3 {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 15px;
  color: #1a1a1a;
}

.service-card p {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  color: #6b7280;
  line-height: 1.6;
}

/* Service Packages Section */
.packages {
  padding: 80px 0;
  background: #f9fafb;
  overflow: hidden;
}

.packages-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
}

.package-column h3 {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 30px;
  text-align: center;
  color: #1a1a1a;
}

.package-item {
  background: white;
  border-radius: 12px;
  padding: 30px;
  margin-bottom: 20px;
  border: 1px solid #e5e7eb;
  transition: all 0.3s ease;
  position: relative;
}

.package-item:hover {
  border-color: #5c27fe;
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(92, 39, 254, 0.1);
}

.package-name {
  font-size: 18px;
  font-weight: 600;
  color: #5c27fe;
  margin-bottom: 10px;
}

.package-price {
  font-size: 24px;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 15px;
}

.package-description {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  color: #6b7280;
  line-height: 1.5;
}

/* Process Section */
.process {
  padding: 80px 0;
}

.process-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
}

.process-step {
  text-align: center;
  padding: 30px 20px;
  position: relative;
}

.process-number {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: linear-gradient(135deg, #5c27fe, #27ae60);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  font-weight: 700;
  color: white !important;
  margin: 0 auto 20px;
  position: relative;
  z-index: 2;
}

.process-step::after {
  content: "";
  position: absolute;
  top: 70px;
  left: calc(50% + 60px);
  width: calc(100% - 80px);
  height: 2px;
  background: linear-gradient(90deg, #5c27fe, #27ae60);
  z-index: 1;
}

.process-step:last-child::after {
  display: none;
}

/* For mobile, hide all connecting lines */
@media (max-width: 768px) {
  .process-step::after {
    display: none;
  }
  .hero.services_hero {
    padding: 20px 0px 20px;
  }
  .hero.services_hero .hero-content {
    flex-direction: column-reverse;
    gap: 0px !important;
  }
  .hero.services_hero .hero-image {
    margin-top: 0px;
    height: 100%;
    transform: none !important;
    aspect-ratio: 1;
  }
  .hero.services_hero .hero-buttons {
    gap: 0px;
  }
  .hero.services_hero .hero-buttons a {
    width: 100%;
  }
  .core-services {
    padding: 20px 0px 20px;
  }
  .packages {
    padding: 20px 0px 20px;
  }
  .process {
    padding: 20px 0px 20px;
  }
  .portfolio {
    padding: 20px 0px 20px;
  }
  .testimonials {
    padding: 20px 0px 20px;
  }
  .cta-section {
    padding: 20px 0px 20px;
  }
  .section-title {
    font-size: 24px !important;
    line-height: 36px;
  }
}

.process-step h4 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 15px;
  color: #1a1a1a;
}

.process-step p {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  color: #6b7280;
  line-height: 1.5;
}

/* Portfolio Section */
.portfolio {
  padding: 80px 0;
  background: #f9fafb;
  position: relative;
  overflow: visible;
}

.portfolio .section-title {
  position: relative;
  z-index: 20;
  margin-bottom: 20px;
}

.portfolio .section-subtitle {
  position: relative;
  z-index: 15;
  margin-bottom: 50px;
}

.portfolio-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  position: relative;
  z-index: 10;
}

.portfolio-item {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
  z-index: 5;
  border: 1px solid #e5e7eb;
}

.portfolio-item:hover {
  transform: translateY(-10px) scale(1.02) !important;
  box-shadow: 0 20px 40px rgba(92, 39, 254, 0.15);
  z-index: 8;
}

.portfolio-image {
  width: 100%;
  height: 200px;
  background: linear-gradient(135deg, #5c27fe, #27ae60, #ff8c42);
  background-size: 200% 200%;
  animation: gradientShift 4s ease infinite;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
  font-size: 16px;
}

@keyframes gradientShift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.portfolio-content {
  padding: 20px;
}

.portfolio-content h4 {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 10px;
  color: #1a1a1a;
}

.portfolio-content p {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  color: #6b7280;
  line-height: 1.5;
}

/* Testimonials Section */
.testimonials {
  padding: 80px 0;
  position: relative;
  overflow: visible;
}

.testimonial-slider {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  position: relative;
  z-index: 5;
}

.testimonial-item {
  background: white;
  border-radius: 12px;
  padding: 40px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  margin: 20px auto;
  position: relative;
  z-index: 5;
  border: 1px solid #e5e7eb;
}

.testimonial-stars {
  color: #ff8c42;
  font-size: 20px;
  margin-bottom: 20px;
}

.testimonial-text {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-style: italic;
  color: #6b7280;
  line-height: 1.6;
  margin-bottom: 20px;
}

.testimonial-author {
  font-weight: 600;
  color: #1a1a1a;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.testimonial-company {
  font-size: 14px;
  color: #5c27fe;
}

/* Final CTA Section */
.final-cta {
  padding: 80px 0;
  background: #5c27fe;
  text-align: center;
  color: white;
}

.final-cta h2 {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 20px;
}

.final-cta p {
  font-size: 18px;
  margin-bottom: 40px;
  opacity: 0.9;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.calendly-embed {
  background: white;
  border-radius: 12px;
  padding: 40px;
  margin: 40px auto 0;
  max-width: 100%;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.calendly-placeholder {
  height: 300px;
  background: #f3f4f6;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #6b7280;
  font-weight: 600;
  border: 2px dashed #d1d5db;
}

.stagger-item {
  opacity: 0;
  filter: blur(6px);
  transform: translateY(40px);
  transition: all 0.6s ease-out;
}

.stagger-item.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0);
}

.stagger-item:nth-child(1) {
  transition-delay: 0.1s;
}
.stagger-item:nth-child(2) {
  transition-delay: 0.2s;
}
.stagger-item:nth-child(3) {
  transition-delay: 0.3s;
}
.stagger-item:nth-child(4) {
  transition-delay: 0.4s;
}
.stagger-item:nth-child(5) {
  transition-delay: 0.5s;
}
.stagger-item:nth-child(6) {
  transition-delay: 0.6s;
}

.blur-glow {
  opacity: 0;
  filter: blur(15px) brightness(0.3);
  transform: scale(0.8);
  transition: all 1.5s cubic-bezier(0.19, 1, 0.22, 1);
}

.blur-glow.visible {
  opacity: 1;
  filter: blur(0px) brightness(1);
  transform: scale(1);
}

.floating-blur {
  opacity: 0;
  filter: blur(20px);
  transform: translateY(80px) rotateX(30deg);
  transition: all 1.8s cubic-bezier(0.23, 1, 0.32, 1);
}

.floating-blur.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0) rotateX(0deg);
}

.text-blur-reveal {
  opacity: 0;
  filter: blur(4px);
  transform: translateY(20px);
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.text-blur-reveal.visible {
  opacity: 1;
  filter: blur(0px);
  transform: translateY(0);
}

/* Responsive Design */
@media (max-width: 768px) {
  .nav-menu {
    display: none;
  }

  .hero h1 {
    font-size: 32px;
  }

  .hero-content {
    flex-direction: column;
    text-align: center;
  }

  .hero-buttons {
    justify-content: center;
  }

  .packages-grid {
    grid-template-columns: 1fr;
  }

  .process-step::after {
    display: none;
  }

  .services-grid {
    grid-template-columns: 1fr;
  }

  .portfolio-grid {
    grid-template-columns: 1fr;
  }

  .process-grid {
    grid-template-columns: 1fr;
  }
}

/* Special Effects */
.magnetic-effect {
  transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.pulse-on-hover:hover {
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

.glow-border {
  position: relative;
  overflow: hidden;
}

.glow-border::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(45deg, #5c27fe, #27ae60, #ff8c42, #5c27fe);
  background-size: 300% 300%;
  border-radius: 14px;
  opacity: 0;
  transition: opacity 0.3s ease;
  animation: borderGlow 3s linear infinite;
  z-index: -1;
}

.glow-border:hover::before {
  opacity: 1;
}

@keyframes borderGlow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.portfoliobtn_wrap {
  text-align: center;
  margin-top: 40px;
  position: relative;
  z-index: 10;
}
.testimonial-slider {
  position: relative;
  z-index: 5;
}
.section-title {
  position: relative;
  z-index: 10;
}
.servicebutton {
  text-align: center;
  margin-top: 50px;
  display: flex;
  justify-content: center;
}

