.ttcl-whn-container {
  width: 100%;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  padding: 80px 20px;
  position: relative;
}

.ttcl-whn-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="rgba%28255,255,255,0.05%29" stroke-width="0.5"/></pattern></defs><rect width="100" height="100" fill="url%28%23grid%29"/></svg>');
  opacity: 0.8;
}

.ttcl-whn-wrapper {
  max-width: 1400px;
  margin: 0 auto;
}

.ttcl-whn-title {
  font-size: 3rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  margin: 0 0 60px 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
}

.ttcl-whn-steps {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
  margin-bottom: 60px;
}

.ttcl-whn-step {
  flex: 0 0 auto;
  width: 280px;
  background: rgb(255 255 255);
  border: 2px solid rgba(167, 139, 250, 0.3);
  border-radius: 24px;
  padding: 50px 30px;
  text-align: center;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.ttcl-whn-step:hover {
  transform: translateY(-5px);
  border-color: #2563eb;
  box-shadow: 0 12px 40px rgba(74, 144, 226, 0.2);
}

.ttcl-whn-icon-wrapper {
  width: 80px;
  height: 80px;
  margin: 0 auto 30px;
  background: linear-gradient(135deg, #2563eb 0%, #1e3c72 100%);
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #1a1a1a;
}

.ttcl-whn-icon-wrapper svg {
  width: 40px;
  height: 40px;
}

.ttcl-whn-step-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #333;
  line-height: 1.4;
  margin: 0;
}

.ttcl-whn-arrow {
  flex: 0 0 auto;
  color: rgba(255, 255, 255);
}

.ttcl-whn-arrow svg {
  width: 40px;
  height: 40px;
  stroke: currentColor;
  stroke-width: 2;
  fill: none;
}

.ttcl-whn-cta {
  text-align: center;
  display: flex;
  justify-content: center;
}

.whn-btn-secondary {
  padding: 16px 32px;
  background: white;
  backdrop-filter: blur(10px);
  color: #333333;
  border-radius: 8px;
  font-weight: 600;
  font-size: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: all 0.3s ease;
}

.whn-btn-secondary:hover {
  background: #2563eb;
  color: white;
  border-color: #2563eb;
}

@media (max-width: 1200px) {
  .ttcl-whn-steps {
    gap: 30px;
  }

  .ttcl-whn-step {
    width: 240px;
    padding: 40px 25px;
  }

  .ttcl-whn-arrow svg {
    width: 35px;
    height: 35px;
  }
}

@media (max-width: 1024px) {
  .ttcl-whn-container {
    padding: 60px 20px;
  }

  .ttcl-whn-title {
    font-size: 2.5rem;
    margin-bottom: 50px;
  }

  .ttcl-whn-steps {
    gap: 20px;
  }

  .ttcl-whn-step {
    width: 220px;
    padding: 35px 20px;
  }

  .ttcl-whn-icon-wrapper {
    width: 70px;
    height: 70px;
    margin-bottom: 25px;
  }

  .ttcl-whn-icon-wrapper svg {
    width: 35px;
    height: 35px;
  }

  .ttcl-whn-step-title {
    font-size: 1rem;
  }

  .ttcl-whn-arrow svg {
    width: 30px;
    height: 30px;
  }
}

@media (max-width: 900px) {
  .ttcl-whn-steps {
    flex-direction: column;
    gap: 30px;
  }

  .ttcl-whn-step {
    width: 100%;
    max-width: 400px;
  }

  .ttcl-whn-arrow {
    transform: rotate(90deg);
  }

  .ttcl-whn-arrow svg {
    width: 35px;
    height: 35px;
  }
}

@media (max-width: 768px) {
  .ttcl-whn-container {
    padding: 50px 15px;
  }

  .ttcl-whn-title {
    font-size: 2rem;
    margin-bottom: 40px;
  }

  .ttcl-whn-step {
    padding: 30px 25px;
  }

  .ttcl-whn-button {
    padding: 16px 40px;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .ttcl-whn-title {
    font-size: 1.75rem;
  }

  .ttcl-whn-step {
    padding: 25px 20px;
  }

  .ttcl-whn-icon-wrapper {
    width: 60px;
    height: 60px;
    margin-bottom: 20px;
  }

  .ttcl-whn-icon-wrapper svg {
    width: 30px;
    height: 30px;
  }

  .ttcl-whn-step-title {
    font-size: 0.95rem;
  }

  .ttcl-whn-button {
    padding: 14px 35px;
    font-size: 0.95rem;
  }
}
